import { useRef, useState, useEffect } from 'react'
import { get_root_url } from '../../utils/utils'
import { Editor } from '@tinymce/tinymce-react';
 
import useCVData from '../../hooks/useCVData';
import useAuth from '../../hooks/useAuth';

import customAsyncDispatcher from '../../hooks/customAsyncDispatcher';

import { useNavigate, Link } from "react-router-dom";

import {toast, Slide} from 'react-toastify';

import FormPreloaderComponent from './FormPreloaderComponent';

import { axiosAuthInstance } from '../../services/http/axios';

let editorElement = null;
let editorEle = null;

const OPENAI_FETCH_URL = '/api/cv/get/openai/fetch';

export default function ExperienceComponent(props) {

    const { blurAndValid, focusAndValid, togglePwd, startLoader, loaderCallback, checkIfNotEmpty, t } = props;

    //START STATE
    const { auth } = useAuth();
    const axios = axiosAuthInstance(auth?.accessToken);    
    
    const [expTab, setExpTab] = useState('');
    const { EXPERIENCE_ACTIONS, experienceData, experienceDispatch, 
        cVData, dispatch, CV_ACTIONS } = useCVData();

    const [ count, setCount ] = useState(1); 
    
    const navigate = useNavigate();
    
    const editorRef = useRef(null);
    const expFormRef = useRef(null);
    const dateRef = useRef();

    const [experienceId, setExperienceId] = useState('');
    const [title, setTitle] = useState('');
    const [company, setCompany] = useState('');
    const [startDate, setStartDate] = useState('');
    const [endDate, setEndDate] = useState('');
    const [currentlyWorking, setCurrentlyWorking] = useState('');
    const [location, setLocation] = useState('');
    const [description, setDescription] = useState('');
    
    const formHeight = useRef('0');
    const [fH, setFH] = useState('0');
    const [isDisplayed, setIsDisplayed] = useState('none');
    const [isDescDisplayed, setIsDescDisplayed] = useState('none');

    useEffect(() => {
        formHeight.current = expFormRef.current.offsetHeight;
            //focusAndValid()

        document.querySelectorAll('input').forEach((item)=>{
            checkIfNotEmpty(item);
        });
    }, [])

    useEffect(() => {
        setFH(parseInt(expFormRef.current.offsetHeight, 10)+20);
    }, [formHeight])

    useEffect(()=>{

        if( !expTab || expTab.trim() == ''){
            return;
        }
        setTitle( experienceData[expTab]?.title ? experienceData[expTab]?.title : ''   );
        setCompany( experienceData[expTab]?.company ? experienceData[expTab]?.company : ''  );
        setLocation( experienceData[expTab]?.location ? experienceData[expTab]?.location : ''  );
        setStartDate( experienceData[expTab]?.startDate ? experienceData[expTab]?.startDate : ''  );
        setEndDate( experienceData[expTab]?.endDate ? experienceData[expTab]?.endDate : ''  );
        setCurrentlyWorking( experienceData[expTab]?.currentlyWorking ? experienceData[expTab]?.currentlyWorking : ''  );
        setDescription( experienceData[expTab]?.description ? experienceData[expTab]?.description : ''  );
        editorRef?.current?.setContent(experienceData[expTab]?.description ? experienceData[expTab]?.description : '' );

    }, [expTab]);

    useEffect(()=>{

         document.querySelectorAll('input').forEach((item)=>{
            checkIfNotEmpty(item);
        });

    }, [experienceData]);

    const resetInputs = () =>{
        setTitle( '' );
        setCompany( '' );
        setLocation( '' );
        setStartDate( '' );
        setEndDate( '' );
        setCurrentlyWorking( '' );
        setDescription( '' );
        editorRef?.current?.setContent('');
    }

    const handleCurrentExperienceData = e =>{
        
        if( !title || title.trim() == '' || !company || company.trim() == '' ){
            console.log('title or company can\'t be empty');
            return;
        }        
        
        let expId = e.target?.dataset?.experienceId;


        experienceDispatch({type: EXPERIENCE_ACTIONS.INSERT_EXPERIENCE_DETAILS, payload:{ 
                    
                    id: expId, title, company, location: location, 
                    startDate, endDate, currentlyWorking, 
                    description: editorRef.current.getContent()

        } })

        //Init New id for form and empty form
        resetInputs();
        setCount(prevCount=>prevCount+1);
        setExperienceId(`new_${count}`);
    }

    const handleEditExperienceData = e =>{
        console.log('inside edit')
        let expId = e.target?.dataset?.experienceId;
        let expTab = e.target?.dataset?.expTab;
        let formExpId  = expFormRef.current.dataset.experienceId;

        let isInArr = experienceData.find((item, index)=>{
            console.log('item.id', item.id);
            console.log('expId', formExpId);
            return item.id == formExpId
        });

        console.log('isInArr', isInArr);

        if( isInArr ){
            if( !title || title.trim() == '' || !company || company.trim() == '' ){
                console.log('title or company can\'t be empty');
                return;
            }
            experienceDispatch({type: EXPERIENCE_ACTIONS.INSERT_EXPERIENCE_DETAILS, payload:{ 
                    
                            id: formExpId, title, company, location: location, 
                            startDate, endDate, currentlyWorking, 
                            description: editorRef.current.getContent()

                } })

        }else{
            if( !(!title || title.trim() == '' || !company || company.trim() == '') ){
               experienceDispatch({type: EXPERIENCE_ACTIONS.INSERT_EXPERIENCE_DETAILS, payload:{ 
                    
                            id: formExpId, title, company, location, 
                            startDate, endDate, currentlyWorking, 
                            description: editorRef.current.getContent()

                } })                
            }
            
        }
        setExpTab(expTab);
        setExperienceId(expId);
        
    }

    //END- STATE  
    function changeField(type){
        
        if( typeof dateRef?.current?.setAttribute !== 'function' ){

            return;
        }

        if( type=="text" ){
            dateRef.current.setAttribute('type', 'text');
            dateRef.current.value = 'Currently Working';
            return;
        }else{
            dateRef.current.setAttribute('type', 'date');    
        }
    
        return endDate;
        //setEndDate('Currently Working')
    }

    const fetchOAISuggestions = async (e) =>{
        e.preventDefault();
        let loaderId = null;    
        let editorVal = '';
        if( title.trim() == '' ){
            toast('Please Enter Title First',{
                type: "error",
                transition: Slide,
                position: "bottom-right",
            })
            return
        }
        //console.log( editorEle.body.innerText )
        if(editorEle?.body?.innerText.trim() != ''){
            editorVal = editorEle.body.innerText.trim()
        }
        let objPrompt ={prompt: `Give one line on ${title} for resume`};
        // if( editorVal !== '' ){
        //     objPrompt = {prompt: `Give one line on ${title} for resume, ${editorVal}`}
        // }
        try {
            loaderId = startLoader(null);

            setIsDescDisplayed('flex');
            const response = await axios.post(OPENAI_FETCH_URL,
                { ...objPrompt }
            );

            if( !response?.data?.succ ){
                setIsDescDisplayed('none');
                loaderCallback(response?.data?.public_msg, loaderId, 'error' );
                return;
            }
            setIsDescDisplayed('none');
            loaderCallback(response?.data?.public_msg, loaderId, 'success' );
            
            editorRef.current.setContent( `${editorRef.current.getContent()} ${response.data.output}` );

        } catch (err) {
            setIsDescDisplayed('none');
            if (!err?.response) {
                loaderCallback('No Server Response', loaderId, 'error' );
                
            } else if (err.response?.status === 400) {
                loaderCallback('Missing Username or Password', loaderId, 'error' );
                
            } else if (err.response?.status === 401) {
                loaderCallback('Unauthorized', loaderId, 'error' );
                
            } else {
                loaderCallback('Something went wrong', loaderId, 'error' );
                
            }
            return;

        }

    }

    const handleExperienceForm = async (e) =>{
        
        e.preventDefault();
            
        let currentExpData = {};
        if( !(!title || title.trim() == '' || !company || company.trim() == '') ){
            let expId = expFormRef.current.dataset.experienceId;
            
            if( !experienceData.find((item)=>{
                return item.id == expId
            }) ){
                    currentExpData =  { 
                            
                            id: expId, title, company, location, 
                            startDate, endDate, currentlyWorking, 
                            description: editorRef.current.getContent()

                }
            }

            // console.log('Hi edid', expId);
            // console.table( 'before experiencedata', experienceData);
            // let resp = await experienceDispatch({type: EXPERIENCE_ACTIONS.INSERT_EXPERIENCE_DETAILS, payload:{ 
                        
            //             id: expId, title, company, location: experienceLocation, 
            //             startDate: experienceStartDate, endDate: experienceEndDate, currentlyWorking, 
            //             description: editorRef.current.getContent()

            // } })

            // console.log('Hii', resp);
            // resetInputs();
            // setCount(prevCount=>prevCount+1);
            // setExperienceId(`new_${count}`);   
        }
      //END

      //console.log('educaitonDationa',Object.keys(currentEdData).length);
      let req = Object.keys(currentExpData).length > 0
                        ? { cv_id: cVData?.cv_id, experience: [ ...experienceData, {...currentExpData} ] }
                        :  { cv_id: cVData?.cv_id, experience: [ ...experienceData ] }

      setIsDisplayed('flex');
      let response = await customAsyncDispatcher(CV_ACTIONS.INSERT_EXPERIENCE, req, auth);
      setIsDisplayed('none');
      if(!response){
            setIsDisplayed('none');
          console.log('something went wrong');
          return;
      }

      dispatch({type: CV_ACTIONS.INSERT_EXPERIENCE, payload: req.experience });  
      navigate(`/resume/${cVData?.cv_id}/education`, {replace: true});
   }

    return (
        <>


            <div className="profile-info-bx">
                <h3>
                    {t('experience')} <img src={`${get_root_url}/assets/images/fi_edit-3.svg`} alt="" />
                </h3>

                <form ref={expFormRef} data-experience-id={experienceId ? experienceId : 'new'} onSubmit={e=>{e.preventDefault()}} className="profile-form">

                    <FormPreloaderComponent loadStyle={{
                                                     height: `${fH}px`,
                                                     display: isDisplayed 
                                                }} />

                    {
                        experienceData?.length > 0 
                            && experienceData.filter((item, index)=>{
                                        return index !== expTab
                                    }).map((item, index)=>{
                                        return (
                                                <div key={item.id} className="form-edit-box">
                                                    <div className="content-hdn-bx">
                                                        <div className='sf-container'>
                                                            <span >
                                                                {item.title}/{item.company}
                                                            </span>
                                                        </div>

                                                        <div onClick={handleEditExperienceData} data-exp-tab={index} data-experience-id={item.id} className='btn btn-cstm'>
                                                            Edit
                                                        </div>
                                                    </div>
                                                </div>
                                            )
                            })
                            
                    }

                    <div className="row">

                        <div className="title col-sm-6 pi-input">
                            <input
                                onChange={ e=>{
                                        experienceDispatch({type: EXPERIENCE_ACTIONS.UPDATE_TITLE, payload: {id: expFormRef.current.dataset.experienceId, data: e.target.value}})
                                        setTitle(e.target.value)
                                        checkIfNotEmpty(e.target)
                                    } } 
                                  value={ title } 
                                type="text" id='title' 
                                onBlur={blurAndValid}
                                onFocus={focusAndValid} 
                                />
                            <label> {t('title')} </label>
                        </div>


                        <div className="com-input col-sm-6 pi-input">
                            <input
                                onChange={ e=>{
                                        experienceDispatch({type: EXPERIENCE_ACTIONS.UPDATE_COMPANY, payload: {id: expFormRef.current.dataset.experienceId, data: e.target.value}})
                                        setCompany(e.target.value)
                                        checkIfNotEmpty(e.target)
                                    } } 
                                  value={ company } 
                                type="text" name='Company'
                                onBlur={blurAndValid}
                                onFocus={focusAndValid} 
                                />
                            <label> {t('company')} </label>
                        </div>

                        <div className="date col-sm-6 pi-input">
                            <input 
                                onChange={ e=>{
                                        experienceDispatch({type: EXPERIENCE_ACTIONS.UPDATE_STARTDATE, payload: {id: expFormRef.current.dataset.experienceId, data: e.target.value}})
                                        setStartDate(e.target.value)
                                        checkIfNotEmpty(e.target)
                                    } } 
                                  value={ startDate } 
                                type="date" name="Start Date" id="" 
                                onBlur={blurAndValid}
                                onFocus={focusAndValid} 
                                />
                            <label> {t('st_date')} </label>
                        </div>

                        

                        <div className="col-sm-6">
                            <div className="row">
                                <div className="date col-7 pi-input">
                                    <input
                                    ref={dateRef} 
                                    type="date" name="End Date" id="" 
                                    onBlur={blurAndValid} 
                                    onFocus={focusAndValid} 
                                    onChange={e=>{
                                        experienceDispatch({type: EXPERIENCE_ACTIONS.UPDATE_TITLE, payload: {id: expFormRef.current.dataset.experienceId, data: e.target.value}})
                                        setEndDate(e.target.value)
                                        checkIfNotEmpty(e.target)
                                    }} 
                                    value={ currentlyWorking ? changeField('text') : changeField('date')}/>
                                    <label > {t('end_date')} </label>
                                </div>

                                <div className="col-5 end-date">
                                    <input 
                                        onChange={ e=>{
                                            
                                            experienceDispatch({type: EXPERIENCE_ACTIONS.UPDATE_WORKING, payload: {id: expFormRef.current.dataset.experienceId, data: e.target.checked}})
                                            setCurrentlyWorking(e.target.checked)
                                            
                                        } } 
                                      checked={ currentlyWorking } 
                                        type="checkbox" 
                                        name='end date' />
                                    <label> {t('cur_working')} </label>
                                </div>
                            </div>

                        </div>

                        <div className="location col-sm-12 pi-input">
                            <input 
                                onChange={ e=>{
                                        experienceDispatch({type: EXPERIENCE_ACTIONS.UPDATE_LOCATION, payload: {id: expFormRef.current.dataset.experienceId, data: e.target.value}})
                                        setLocation(e.target.value)

                                    } } 
                                  value={ location } 
                                type="text" name='Location' 
                                onBlur={blurAndValid}
                                onFocus={focusAndValid} 
                                />
                            <label> {t('location')} </label>
                        </div>


                        <div className="col-sm-12 pi-input">
                            <FormPreloaderComponent loadStyle={{
                                                     display: isDescDisplayed,
                                                     height: '100%',
                                                    opacity: '0.7',
                                                    top: '50%',
                                                    left: '50%',
                                                    transform: 'translate(-50%,-50%)' 
                                                }} />
                            <div className="desc-head">
                                <p>
                                    {t('description')}
                                </p>

                                <div onClick={fetchOAISuggestions} className='btn btn-cstm smal-btn'>
                                    {t('ai_write')}
                                </div>

                                <span className='bk-edits-btn smal-btn outline-btn'>3k</span>
                            </div>

                            <Editor
                                apiKey="rajgntcz3aruwm9gfxfpeogddnbgbznhopgloeursrk6tndt"
                                onInit={(evt, editor) => {

                                    setTimeout(()=>{

                                                        editorEle = document.querySelector('.tox-edit-area__iframe').contentWindow.document;
                                                        console.log('editorEle', editorEle);
                                                        editorEle.head.innerHTML = `<style>
                                                            @import url('https://fonts.googleapis.com/css2?family=Maven+Pro:wght@400;500;600&display=swap');
                                                            
                                                            .typewriter-text {
                                                                                user-select: none;
                                                                                overflow: hidden;
                                                                                vertical-align: middle;
                                                                              animation: typing 3s steps(30, end);
                                                                                white-space: nowrap;
                                                                                
                                                                                color:#8896e1;
                                                                                /*border-right: 4px solid orange;*/
                                                                                box-sizing: border-box;
                                                                            }

                                                                            @keyframes typing {
                                                                                from { 
                                                                                    width: 0% 
                                                                                }
                                                                                to { 
                                                                                    width: 100% 
                                                                                }
                                                                            }
                                                            .mce-content-body {
                                                                font-size: 16px;
                                                                font-weight: 500;
                                                                color: #828282;
                                                                font-family: 'Maven Pro';
                                                            }
                                                        </style>  ${editorEle.head.innerHTML}`;
                                                        // editorElement = editorEle.querySelector('#tinymce');
                                                        console.log('editorElement', editorElement);
                                                        // editorElement.innerHTML = `${editorElement.innerHTML}`;
                                                        // editorElement.querySelector('p').innerHTML += `<p class="typewriter-text">Content is changed</p>`;
                                                        //editorElement.setAttribute('contenteditable', false);
                                                    },1000);

                                    return editorRef.current = editor
                                }}
                                initialValue={experienceData.description}
                                onEditorChange={ content=>{
                                            experienceDispatch({type: EXPERIENCE_ACTIONS.UPDATE_DESCRIPTION, payload: {id: expFormRef.current.dataset.experienceId, data: content}})
                                            setDescription(content)
                                        } } 
                                init={{
                                    height: 150,
                                    menubar: false,
                                    branding: false,
                                    skin: 'material-classic',
                                    content_css: 'material-classic',
                                    icons: 'material',
                                    selector: 'textarea',  // change this value according to your HTML
                                    //content_style: 'body { margin: 10px; border: 5px solid red; padding: 3px; background: red; }',
                                    //content_css: get_root_url+"assets/css/text-area.css",
                                    statusbar: false,
                                    plugins: [
                                        'lists link print preview',
                                        
                                        
                                    ],
                                    toolbar: 
                                        'bold italic underline | bullist numlist |' +
                                        'alignjustify alignleft aligncenter alignright  ',
                                    content_style: 'body { font-family:Helvetica,Arial,sans-serif; font-size:12px }'
                                }}
                            />

                        </div>

                    </div>

                    <div className="row btm-btn-sec expsec">

                        <div className="more-exp-btn">
                                <div onClick={handleCurrentExperienceData} data-experience-id={ experienceId ? experienceId : 'new' } className='txt-btn'>
                                    {t('add_mExp')}
                                </div>
                        </div>

                        <div className="col-sm-4 col-6 btn-mt txt576r">
                            <Link to={`/resume/${cVData.cv_id}/contact-info`} className='btn bk-edits-btn'>
                                {t('back')}
                            </Link>
                        </div>

                        <div className="col-sm-4 col-6 btn-mt">
                            <div type="submit" data-experience-id={ experienceId ? experienceId : "new" } onClick={handleExperienceForm} className='btn btn-cstm'>
                                {t('save')}
                            </div>
                        </div>
                    </div>




                </form>


            </div>

        </>
    )
}
