import { useRef, useState, useEffect, useReducer } from 'react'
import { get_root_url } from '../../utils/utils'
import { Editor } from '@tinymce/tinymce-react';

import {toast, Slide} from 'react-toastify';

import useAuth from '../../hooks/useAuth';
import useCVData from '../../hooks/useCVData';

import customAsyncDispatcher from '../../hooks/customAsyncDispatcher';

import FormPreloaderComponent from './FormPreloaderComponent';

import { useNavigate, Link } from "react-router-dom";

import useLang from '../../hooks/useLang';

export default function EducationComponent(props) {
//     //START STATE
    const { blurAndValid, focusAndValid, togglePwd, startLoader, loaderCallback, checkIfNotEmpty } = props;

    const {t} = useLang();
    const { auth } = useAuth();
    const editorRef = useRef(null);    
    const edFormRef = useRef(null);
    const [edTab, setEdTab] = useState('');
    const { EDUCATION_ACTIONS, educationData, educationDispatch, 
        cVData, dispatch, CV_ACTIONS } = useCVData(); 

    const [ count, setCount ] = useState(1);
    //console.log("current cv data", cVData);

    const [educationId, setEducationId] = useState('');
    const [degree, setDegree] = useState('');
    const [college, setCollege] = useState('');
    const [educationLocation, setEducationLocation] = useState('');
    const [educationStartDate, setEducationStartDate] = useState('');
    const [educationEndDate, setEducationEndDate] = useState('');
    const [currentlyStudying, setCurrentlyStudying] = useState('');
    const [educationDescription, setEducationDescription] = useState('');

    const dateRef = useRef();
    const formHeight = useRef('0');
    const [fH, setFH] = useState('0');
    const [isDisplayed, setIsDisplayed] = useState('none');

    useEffect(() => {
        formHeight.current = edFormRef.current.offsetHeight;
            //focusAndValid()

        document.querySelectorAll('input').forEach((item)=>{
            checkIfNotEmpty(item);
        });
    }, [])

    useEffect(() => {
        setFH(parseInt(edFormRef.current.offsetHeight, 10)+20);
    }, [formHeight])

    useEffect(()=>{

         document.querySelectorAll('input').forEach((item)=>{
            checkIfNotEmpty(item);
        });

    }, [educationData]);

    const checkDates = () =>{
        let currSDate = new Date(educationStartDate);
        let currEDate = new Date(educationEndDate);
        return +currSDate === +currEDate || currSDate > currEDate
            
        
    }
    useEffect(()=>{
        if( checkDates() ){
            toast('Start Date can\'t be greater than End Date', {
                type:"error",
                transition: Slide,
                position: "bottom-right",
            } )
            setEndDate(null);
            dateRef.current.value = '';    
        } 
    }, [educationStartDate, educationEndDate]);

    useEffect(()=>{

        if( !edTab || edTab.trim() == ''){
            return;
        }
        setDegree( educationData[edTab]?.degree ? educationData[edTab]?.degree : ''   );
        setCollege( educationData[edTab]?.college ? educationData[edTab]?.college : ''  );
        setEducationLocation( educationData[edTab]?.location ? educationData[edTab]?.location : ''  );
        setEducationStartDate( educationData[edTab]?.startDate ? educationData[edTab]?.startDate : ''  );
        setEducationEndDate( educationData[edTab]?.endDate ? educationData[edTab]?.endDate : ''  );
        setCurrentlyStudying( educationData[edTab]?.currentlyStudying ? educationData[edTab]?.currentlyStudying : ''  );
        setEducationDescription( educationData[edTab]?.description ? educationData[edTab]?.description : ''  );
        editorRef?.current?.setContent(educationData[edTab]?.description ? educationData[edTab]?.description : '' );

    }, [edTab]);



    const resetInputs = () =>{
        setDegree( '' );
        setCollege( '' );
        setEducationLocation( '' );
        setEducationStartDate( '' );
        setEducationEndDate( '' );
        setCurrentlyStudying( '' );
        setEducationDescription( '' );
        editorRef?.current?.setContent('');
    }

    // const handleCurrentEducationData = e =>{

    //     // let edId = e.target?.dataset?.educationId;
    //     // //console.log('degree', degree)
    //     // if( !e.target.dataset?.edTab && (!degree || degree.trim() == '' || !college || college.trim() == '') ){
    //     //     console.log('degree or college can\'t be empty');
    //     //     return;
    //     // }

    //     // if(e.target.dataset?.edTab && (!degree || degree.trim() == '' || !college || college.trim() == '') ){
    //     //     //Modify code for better conditions

    //     // }else{
            
    //     //     // edId && edId.split('_')[0] == 'new'
    //     //     let id = edId && edId.split('_')[0] == 'new' ? `new_${count}` : educationId;
                
    //     //     educationDispatch({type: EDUCATION_ACTIONS.INSERT_EDUCATION_DETAILS, payload:{ 
                        
    //     //                 id, degree, college, location: educationLocation, 
    //     //                 startDate: educationStartDate, endDate: educationEndDate, currentlyStudying, 
    //     //                 description: editorRef.current.getContent()

    //     //     } })
    //     // }
            
    //     // if(e.target.dataset?.edTab && e.target.dataset?.edTab.trim() != '' ){
    //     //     setEdTab( e.target.dataset?.edTab ); 
    //     //     setEducationId( edId );
    //     // } else{

    //     //     console.log('I am hereee');
    //     //     //setCount(prevCount=>prevCount+1);
    //     //     setEdTab('');
    //     //     setEducationId( `new_${count}` );
    //     // }
    //     // setCount(prevCount=>prevCount+1);
    // }

    const handleCurrentEducationData = e =>{
        
        if( !degree || degree.trim() == '' || !college || college.trim() == '' ){
            console.log('degree or college can\'t be empty');
            return;
        }        
        
        let edId = e.target?.dataset?.educationId;


        educationDispatch({type: EDUCATION_ACTIONS.INSERT_EDUCATION_DETAILS, payload:{ 
                    
                    id: edId, degree, college, location: educationLocation, 
                    startDate: educationStartDate, endDate: educationEndDate, currentlyStudying, 
                    description: editorRef.current.getContent()

        } })

        //Init New id for form and empty form
        resetInputs();
        setCount(prevCount=>prevCount+1);
        setEducationId(`new_${count}`);
    }

    const handleEditEducationData = e =>{
        console.log('inside edit')
        let edId = e.target?.dataset?.educationId;
        let edTab = e.target?.dataset?.edTab;
        let formEdId  = edFormRef.current.dataset.educationId;

        let isInArr = educationData.find((item, index)=>{
            console.log('item.id', item.id);
            console.log('edId', formEdId);
            return item.id == formEdId
        });

        console.log('isInArr', isInArr);

        if( isInArr ){
            if( !degree || degree.trim() == '' || !college || college.trim() == '' ){
                console.log('degree or college can\'t be empty');
                return;
            }
            educationDispatch({type: EDUCATION_ACTIONS.INSERT_EDUCATION_DETAILS, payload:{ 
                    
                            id: formEdId, degree, college, location: educationLocation, 
                            startDate: educationStartDate, endDate: educationEndDate, currentlyStudying, 
                            description: editorRef.current.getContent()

                } })

        }else{
            if( !(!degree || degree.trim() == '' || !college || college.trim() == '') ){
               educationDispatch({type: EDUCATION_ACTIONS.INSERT_EDUCATION_DETAILS, payload:{ 
                    
                            id: formEdId, degree, college, location: educationLocation, 
                            startDate: educationStartDate, endDate: educationEndDate, currentlyStudying, 
                            description: editorRef.current.getContent()

                } })                
            }
            
        }
        setEdTab(edTab);
        setEducationId(edId);
        
    }

    //END- STATE  
    const navigate = useNavigate();

    const log = () => {
        if (editorRef.current) {
            console.log(editorRef.current.getContent());
        }
    };

    const [EndDate, setEndDate] = useState('');


    const handleEndDate = e => {
        let value = e.target.value;
        setEndDate(value);
    }

    const handleEducationForm = async (e) =>{
        e.preventDefault();
        
        if( checkDates() ){
            toast('Start Date can\'t be greater than End Date', {
                type:"error",
                transition: Slide,
                position: "bottom-right",
            } )
            return    
        } 

        let currentEdData = {};
        if( !(!degree || degree.trim() == '' || !college || college.trim() == '') ){
            let edId = edFormRef.current.dataset.educationId;
            
            if( !educationData.find((item)=>{
                return item.id == edId
            }) ){
                    currentEdData =  { 
                            
                            id: edId, degree, college, location: educationLocation, 
                            startDate: educationStartDate, endDate: educationEndDate, currentlyStudying, 
                            description: editorRef.current.getContent()

                }
            }

            // console.log('Hi edid', edId);
            // console.table( 'before educationdata', educationData);
            // let resp = await educationDispatch({type: EDUCATION_ACTIONS.INSERT_EDUCATION_DETAILS, payload:{ 
                        
            //             id: edId, degree, college, location: educationLocation, 
            //             startDate: educationStartDate, endDate: educationEndDate, currentlyStudying, 
            //             description: editorRef.current.getContent()

            // } })

            // console.log('Hii', resp);
            // resetInputs();
            // setCount(prevCount=>prevCount+1);
            // setEducationId(`new_${count}`);   
        }
      //END

      //console.log('educaitonDationa',Object.keys(currentEdData).length);
      let req = Object.keys(currentEdData).length > 0
                        ? { cv_id: cVData?.cv_id, education: [ ...educationData, {...currentEdData} ] }
                        :  { cv_id: cVData?.cv_id, education: [ ...educationData ] }

      setIsDisplayed('flex');
      let response = await customAsyncDispatcher(CV_ACTIONS.INSERT_EDUCATION, req, auth);
      if(!response){
            setIsDisplayed('none');
          console.log('something went wrong');
          return;
      }
      setIsDisplayed('none');
      dispatch({type: CV_ACTIONS.INSERT_EDUCATION, payload: req.education }); 
      navigate(`/resume/${cVData?.cv_id}/skill-lang`, {replace: true});  
      
   }


    return (
        <>
            <div className="profile-info-bx">
                <h3>
                    {t('edu_info')} <img src={`${get_root_url}/assets/images/fi_edit-3.svg`} alt="" />
                </h3>

                <form ref={edFormRef} data-education-id={educationId ? educationId : 'new'} className="profile-form" onSubmit={e=>{e.preventDefault()}}>

                    <FormPreloaderComponent loadStyle={{
                                                     height: `${fH}px`,
                                                     display: isDisplayed 
                                                }} />

                    {
                        educationData?.length > 0 
                            && educationData.filter((item, index)=>{
                                        return index !== edTab
                                    }).map((item, index)=>{
                                        return (
                                                <div key={item.id} className="form-edit-box">
                                                    <div className="content-hdn-bx">
                                                        <div className='sf-container'>
                                                            <span >
                                                                {item.degree}/{item.college}
                                                            </span>
                                                        </div>

                                                        <button onClick={handleEditEducationData} data-ed-tab={index} data-education-id={item.id} className='btn btn-cstm'>
                                                            Edit
                                                        </button>
                                                    </div>
                                                </div>
                                            )
                            })
                            
                    }
                    
                    <div className="row">

                        <div className="title col-sm-6 pi-input">
                            <input
                                onChange={ e=>{
                                        educationDispatch({type: EDUCATION_ACTIONS.UPDATE_DEGREE, payload: {id: edFormRef.current.dataset.educationId, data: e.target.value}}) 
                                        setDegree(e.target.value)
                                    } } 
                                  value={ degree }  
                                type="text" id='Degree'
                                onBlur={blurAndValid}
                                onFocus={focusAndValid}
                                />
                            <label> {t('degree')} </label>
                        </div>


                        <div className="com-input col-sm-6 pi-input">
                            <input
                                onChange={ e=>{
                                        educationDispatch({type: EDUCATION_ACTIONS.UPDATE_COLLEGE, payload: {id: edFormRef.current.dataset.educationId, data: e.target.value}})
                                        setCollege(e.target.value)
                                    } } 
                                  value={ college }  
                                type="text" name='College'  
                                onBlur={blurAndValid}
                                onFocus={focusAndValid}
                                />
                            <label> {t('college')} </label>
                        </div>

                        <div className="location col-sm-12 pi-input">
                            <input
                                onChange={ e=>{
                                        educationDispatch({type: EDUCATION_ACTIONS.UPDATE_LOCATION, payload: {id: edFormRef.current.dataset.educationId, data: e.target.value}})
                                        setEducationLocation(e.target.value)
                                    } } 
                                  value={ educationLocation }  
                                type="text" name='location'  
                                onBlur={blurAndValid}
                                onFocus={focusAndValid}
                                />
                            <label> {t('location')} </label>
                        </div>

                        <div className="date col-sm-6 pi-input">
                            <input
                                onChange={ e=>{
                                        educationDispatch({type: EDUCATION_ACTIONS.UPDATE_STARTDATE, payload: {id: edFormRef.current.dataset.educationId, data: e.target.value}})
                                        setEducationStartDate(e.target.value)
                                    } } 
                                  value={ educationStartDate }  
                                type="date" name="startDate" id=""  
                                onBlur={blurAndValid}
                                onFocus={focusAndValid}
                                />
                            <label> {t('st_date')} </label>
                        </div>

                        <div className="date col-sm-6 pi-input">
                            <input
                                ref={dateRef}
                                onChange={ e=>{
                                        educationDispatch({type: EDUCATION_ACTIONS.UPDATE_ENDDATE, payload: {id: edFormRef.current.dataset.educationId, data: e.target.value}})
                                        setEducationEndDate(e.target.value)
                                    } } 
                                  value={ educationEndDate }  
                                type="date" name="endDate" id=""  
                                onBlur={blurAndValid}
                                onFocus={focusAndValid}
                                />
                            <label> {t('end_date')} </label>
                        </div>                        


                        <div className="col-sm-12 pi-input">

                            <div className="desc-head">
                                <p>
                                    {t('description')}
                                </p>

                                <button className='btn btn-cstm smal-btn'>
                                    {t('ai_write')}
                                </button>

                                <span className='bk-edits-btn smal-btn outline-btn'>3k</span>
                            </div>

                            <Editor
                                apiKey="rajgntcz3aruwm9gfxfpeogddnbgbznhopgloeursrk6tndt"
                                onInit={(evt, editor) => editorRef.current = editor}
                                initialValue={educationData.description}
                                init={{
                                    height: 150,
                                    menubar: false,
                                    branding: false,
                                    skin: 'material-classic',
                                    content_css: 'material-classic',
                                    icons: 'material',
                                    selector: 'textarea',  // change this value according to your HTML
                                    //content_style: 'body { margin: 10px; border: 5px solid red; padding: 3px; background: red; }',
                                    //content_css: get_root_url+"assets/css/text-area.css",
                                    statusbar: false,
                                    plugins: [
                                        'lists link print preview',


                                    ],
                                    toolbar:
                                        'bold italic underline | bullist numlist |' +
                                        'alignjustify alignleft aligncenter alignright  ',
                                    content_style: 'body { font-family:Helvetica,Arial,sans-serif; font-size:12px }'
                                }}
                                onEditorChange={content=>{
                                    educationDispatch({type: EDUCATION_ACTIONS.UPDATE_DESCRIPTION, payload: {id: edFormRef.current.dataset.educationId, data: content}})
                                }}
                            />

                        </div>

                    </div>

                    <div className="row btm-btn-sec expsec">

                        <div className="more-exp-btn">
                            <div onClick={handleCurrentEducationData} data-education-id={ educationId ? educationId : 'new' } className='txt-btn'>
                                {t('add_mEdu')}
                            </div>
                        </div>

                        <div className="col-sm-4 col-6 btn-mt txt576r">
                            <Link to={`/resume/${cVData.cv_id}/experience`} >
                                <div className='btn bk-edits-btn'>
                                    {t('back')}
                                </div>
                            </Link>
                        </div>

                        <div className="col-sm-4 col-6 btn-mt">
                            <div type="submit" data-education-id={ educationId ? educationId : "new" } onClick={handleEducationForm} className='btn btn-cstm'>
                                {t('save')}
                            </div>
                        </div>
                    </div>




                </form>


            </div> 

        </>
    )
}
