import { get_root_url } from '../../utils/utils'

export default function TemplatePopupComponent() {
  return (
    <>
          

          {/* <!-- Modal --> */}
          <div className="modal fade" id="templatePreview" tabindex="-1" aria-labelledby="templatePreviewLabel" aria-hidden="true">
              <div className="modal-dialog preview-box">
                  <div className="modal-content">
                      <div className="modal-header">
                           <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                      </div>
                      <div className="modal-body">
                          <img src={`${get_root_url}/assets/images/popup.svg`} alt="Template Preview" />
                      </div>
                  </div>
              </div>
          </div>
      
    </>
  )
}
