import { get_root_url } from '../../utils/utils';
import classNames from 'classnames';

import useAuth from '../../hooks/useAuth';
import useLang from '../../hooks/useLang';

import {Link} from 'react-router-dom';

import { toast, Slide } from 'react-toastify';

export default function HeaderComponent(props) { 

	const closeFunc = e => {
		let closeBtn = document.querySelector('.navbar-toggler');
		closeBtn.classList.toggle('closeIcon');
	};

	const { setAuth } = useAuth();

	const {i18next, languages, t, currentLanguageCode ,currentLanguage} = useLang();

	console.log( 'Country code', currentLanguage);
	return (

		<header>
			<nav className="navbar navbar-expand-lg navbar-light">
				<div className="container-fluid pddng-23">
					<a className="navbar-brand" href="#">
						<img src={`${get_root_url}/assets/images/logo.svg`} alt="logo" />
					</a>
					{/* mobile language */}

					<li className="nav-item dropdown d-block d-lg-none">

						<a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown"
							aria-expanded="false">
							<span
								className={`flag-icon flag-icon-${currentLanguage['country_code']} mx-2`}
								style={{
									aspectRatio: "4/4",
									backgroundSize: "cover",
									borderRadius: "50%",
									verticalAlign: "middle"
								}}
							></span>
							{/*<img src={`${get_root_url}/assets/images/user.png`} alt=""/>*/}
							{currentLanguage['name']}
						</a>
						<ul className="dropdown-menu" aria-labelledby="navbarDropdown">
							<li>
								<span className="dropdown-item-text">{t('language')}</span>
							</li>
							{languages.map(({ code, name, country_code }) => (
								<li key={country_code}>
									<a
										href="#"
										className={classNames('dropdown-item', {
											disabled: currentLanguageCode === code,
										})}
										onClick={() => {
											i18next.changeLanguage(code)
										}}
									>
										<span
											className={`flag-icon flag-icon-${country_code} mx-2`}
											style={{
												opacity: currentLanguageCode === code ? 0.5 : 1,
											}}
										></span>
										{name}
									</a>
								</li>
							))}
						</ul>
					</li>



					<button className="navbar-toggler" onClick={closeFunc} type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent"
						aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
						<span className="navbar-toggler-icon"></span>
					</button>
					<div className="collapse navbar-collapse" id="navbarSupportedContent">

						
						<ul className="navbar-nav ms-auto mb-2 mb-lg-0">

							<li class="nav-item dash-link d-none d-lg-block">
								<Link class="nav-link active" to={'/dashboard'} >Dashboard</Link>
							</li>

							<li class="nav-item d-lg-none">
								<Link class="nav-link" to={'/dashboard'} >Upgrade</Link>
							</li>

							<li class="nav-item d-lg-none">
								<Link class="nav-link" to={'/dashboard'} >Token information</Link>
							</li>

							<li class="nav-item d-lg-none">
								<Link class="nav-link " to={'/dashboard'} >Setting</Link>
							</li>

							<li class="nav-item d-lg-none">
								<Link class="nav-link " to={'/dashboard'} >Contact us</Link>
							</li>

							<li class="nav-item d-lg-none">
								<Link class="nav-link " to={'/dashboard'} >Sign out</Link>
							</li>


							<li className="nav-item dropdown d-none d-lg-block">

								<a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button"
									data-bs-toggle="dropdown" aria-expanded="false">
									<img src={`${get_root_url}/assets/images/Group571.svg`} alt=""/>
									Upgrade
								</a>
								<ul className="dropdown-menu" aria-labelledby="navbarDropdown">
									<li><a className="dropdown-item" href="#"> Upgrade </a></li>
									<li><a className="dropdown-item" href="#"> Token information </a></li>
								</ul>
							</li>

							<li className="nav-item dropdown d-none d-lg-block">

								<a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown"
									aria-expanded="false">
									<span
				                      className={`flag-icon flag-icon-${currentLanguage['country_code']} mx-2`}
				                      style={{    	
				                      				aspectRatio: "4/4",
												    backgroundSize: "cover",
												    borderRadius: "50%",
												    verticalAlign: "middle"
											}}
				                    ></span>
									{/*<img src={`${get_root_url}/assets/images/user.png`} alt=""/>*/}
										{currentLanguage['name']}
								</a>
								<ul className="dropdown-menu" aria-labelledby="navbarDropdown">
									<li>
						            	<span className="dropdown-item-text">{t('language')}</span>
						            </li>
						            {languages.map(({ code, name, country_code }) => (
						                <li key={country_code}>
						                  <a
						                    href="#"
						                    className={classNames('dropdown-item', {
						                      disabled: currentLanguageCode === code,
						                    })}
						                    onClick={() => {
						                      i18next.changeLanguage(code)
						                    }}
						                  >
						                    <span
						                      className={`flag-icon flag-icon-${country_code} mx-2`}
						                      style={{
						                        opacity: currentLanguageCode === code ? 0.5 : 1,
						                      }}
						                    ></span>
						                    {name}
						                  </a>
						                </li>
						              ))}
								</ul>
							</li>

							<li className="nav-item dropdown d-none d-lg-block">

								<a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown"
									aria-expanded="false">
									<img src={`${get_root_url}/assets/images/user.png`} alt="user" />
									Sean Benet
								</a>
								<ul className="dropdown-menu" aria-labelledby="navbarDropdown">
									<li><div className="dropdown-item" href="#">Setting</div></li>
									<li><div className="dropdown-item" href="#">Contact us</div></li>
									<li><div onClick={ e=>{  localStorage.removeItem('refresh_token'); setAuth(null); toast('Logged Out Successfully', {
																										                type:"success",
																										                transition: Slide,
																										                position: "bottom-right",
																										            } ) } } className="dropdown-item" href="#">Sign out</div></li>
								</ul>
							</li>

						</ul>
					</div>
				</div>
			</nav>
		</header>
	);
}