import Guard from './Guard';
import PersistLogin from './PersistLogin';

import CVDataExposer from './CVDataExposer';

import CheckParamsForCV from './CheckParamsForCV';

import { Navigate, Routes, Route } from 'react-router-dom';

import { ToastContainer } from 'react-toastify';

import RegistrationLayout from './components/layouts/RegistrationLayout/RegistrationLayout';
import TemplateArea from './components/layouts/TemplateArea/TemplateArea';
import AdminArea from './components/layouts/AdminLayout/AdminLayout';

import MissingComponent from './components/components/MissingComponent';

import SocialLoginComponent from './components/components/SocialLoginComponent';
import CheckLoginComponent from './components/components/CheckLoginComponent';

import LoginPage from './components/pages/LoginPage/LoginPage';
import RegistrationPage from './components/pages/RegistrationPage/RegistrationPage';
import ForgotPage from './components/pages/ForgotPage/ForgotPage';
import NewPassword from './components/pages/NewPassword/NewPassword';
import TemplatePage from './components/pages/TemplatePage/TemplatePage';  
import ContactUsPage from './components/pages/DashBoard/ContactUs/ContactUsPage';  


import ResumePage from './components/pages/ResumeEditor/ResumePage';
import ProfileInfoPage from './components/pages/ResumeEditor/ProfileInfoPage';
import ExperiencePage from './components/pages/ResumeEditor/ExperiencePage';
import EducationPage from './components/pages/ResumeEditor/EducationPage';
import SkillPage from './components/pages/ResumeEditor/SkillPage';
import ObjectivePage from './components/pages/ResumeEditor/ObjectivePage';
import AddSectionPage from './components/pages/ResumeEditor/AddSectionPage';
import EditSectionPage from './components/pages/ResumeEditor/EditSectionPage';
import MoreSectionPage from './components/pages/ResumeEditor/MoreSectionPage';
import DashBoardPage from './components/pages/DashBoard/DashBoardPage';
import TermsPage from './components/pages/TermsPage';
import PrivacyPage from './components/pages/PrivacyPage';
import DisclaimerPage from './components/pages/DisclaimerPage';
import AdminLayout from './components/layouts/AdminLayout/AdminLayout';
import UserSettingPage from './components/pages/DashBoard/UserSettingPage';

const AppWithRegistrationLayout = RegistrationLayout(App);
const LoginPageWithRegistrationLayout = RegistrationLayout(LoginPage);
const RegistrationPageWithRegistrationLayout = RegistrationLayout(RegistrationPage);
const ForgotPageWithRegistrationLayout = RegistrationLayout(ForgotPage);
const NewPasswordWithRegistrationLayout = RegistrationLayout(NewPassword);

const TemplatePageWithTemplateArea = TemplateArea(TemplatePage);

const ProfileInfoPageWithTemplateArea = TemplateArea(ProfileInfoPage);
const ExperiencePageWithTemplateArea = TemplateArea(ExperiencePage);
const EducationPageWithTemplateArea = TemplateArea(EducationPage);
const SkillPageWithTemplateArea = TemplateArea(SkillPage);
const ObjectivePageWithTemplateArea = TemplateArea(ObjectivePage);
const AddSectionPageWithTemplateArea = TemplateArea(AddSectionPage);
const EditSectionPageWithTemplateArea = TemplateArea(EditSectionPage);
const MoreSectionPageWithTemplateArea = TemplateArea(MoreSectionPage);
const ResumePageWithTemplateArea = TemplateArea(ResumePage);
const DashBoardPageWithTemplateArea = TemplateArea(DashBoardPage);
const TermsPageWithTemplateArea = TemplateArea(TermsPage);
const PrivacyPageWithTemplateArea = TemplateArea(PrivacyPage);
const DisclaimerPageWithTemplateArea = TemplateArea(DisclaimerPage);

const ContactUsPageWithTemplateArea = AdminLayout(ContactUsPage);

const UserSettingPageWithTemplateArea = AdminLayout(UserSettingPage);



export default function App() {
  return (

        <>
        <Routes >

          <Route path="/" element={<Navigate to='/login' replace={true} />} />

        <Route element={< CVDataExposer />} >
          <Route path='terms' element={<DisclaimerPageWithTemplateArea />} />

          <Route path='contact-us' element={<ContactUsPageWithTemplateArea />} />
          <Route path='user-setting' element={<UserSettingPageWithTemplateArea />} />

          <Route path='newtemp' element={<TemplatePageWithTemplateArea />} />
          <Route path="contact-info" element={<ProfileInfoPageWithTemplateArea />} />
          <Route path="experience" element={<ExperiencePageWithTemplateArea />} />
          <Route path="education" element={<EducationPageWithTemplateArea />} />
          <Route path="skill-lang" element={<SkillPageWithTemplateArea />} />
          <Route path="objective" element={<ObjectivePageWithTemplateArea />} />
          <Route path="add-section" element={<AddSectionPageWithTemplateArea />} />
          <Route path="more-section" element={<MoreSectionPageWithTemplateArea />} />
          <Route path="edit-section" element={<EditSectionPageWithTemplateArea />} />
        </Route>
        

          <Route path="/login/social/" element={<Navigate to="/login" replace={true} />} />
          <Route path="/login/social/:refresh_token" element={<SocialLoginComponent />} />
        
          <Route path="/forgot" element={<ForgotPageWithRegistrationLayout />} />
          <Route path="/password/" element={<Navigate to='/login' replace={true} />} />
          <Route path="/password/:token" element={<NewPasswordWithRegistrationLayout />} />
          
          <Route element={<PersistLogin/>}>

            <Route element={<CheckLoginComponent/>} >
              <Route path="/login" element={<LoginPageWithRegistrationLayout />} />
              <Route path="/register" element={<RegistrationPageWithRegistrationLayout />} />
            </Route>

            <Route element={<Guard />}>
              <Route element={< CVDataExposer />} >

                <Route path="/dashboard" element={<DashBoardPageWithTemplateArea />} />

                <Route path="/resume" element={<TemplatePageWithTemplateArea/>} />
                
                <Route path="/resume/templates"  >
                  <Route index element={<TemplatePageWithTemplateArea />} />
                  <Route path=":cv_id" element={<TemplatePageWithTemplateArea />} />                  
                </Route>

                <Route path="/resume/:cv_id" element={ <CheckParamsForCV/> } >
                  <Route index element={<ProfileInfoPageWithTemplateArea/>} />
                  <Route path="contact-info" element={<ProfileInfoPageWithTemplateArea/>} />
                  <Route path="experience" element={<ExperiencePageWithTemplateArea/>} />
                  <Route path="education" element={<EducationPageWithTemplateArea/>} />
                  <Route path="skill-lang" element={<SkillPageWithTemplateArea/>} />
                  <Route path="objective" element={<ObjectivePageWithTemplateArea/>} />
                  <Route path="add-section" element={<AddSectionPageWithTemplateArea/>}>
                    <Route index element={<AddSectionPageWithTemplateArea/>} />
                    <Route path=":title" element={<AddSectionPageWithTemplateArea/>} />
                  </Route>
                  <Route path="more-section" element={<MoreSectionPageWithTemplateArea/>} />
                  <Route path="edit-section" element={<EditSectionPageWithTemplateArea/>} />
                </Route>
              </Route>
            </Route>
          </Route>

          {/* catch all */}
          <Route path="*" element={<MissingComponent />} />

        </Routes>
        <ToastContainer />
        </>
    )
}
