import React, { useRef, useState, useEffect } from 'react';
//import {useStyles} from '../../styles/styles';
import { useNavigate, useLocation, Link } from 'react-router-dom';

import { get_root_url } from '../../utils/utils';
import axiosPublic from '../../services/http/axios';

import useAuth from '../../hooks/useAuth';
import { Slide, toast } from 'react-toastify';

//const USER_EMAIL_REGEX = /^[A-z][A-z0-9-_]{3,23}$/;
//const PWD_REGEX = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%]).{8,24}$/;
const REGISTER_URL = 'api/users/register';
 
export default function RegistrationComponent( props ) {


    const { blurAndValid, focusAndValid, togglePwd, notify } = props;
    //START- use states and effects
    const { setAuth } = useAuth();

    const navigate = useNavigate();
    const location = useLocation();
    const from = location.state?.from?.pathname || "/dashboard";
    //const from = '/dashboard';

    const userNameRef = useRef();
    const userEmailRef = useRef();
    const errRef = useRef();

    const [userName, setUserName] = useState('');
    const [validName, setValidName] = useState(false);
    const [userNameFocus, setUserNameFocus] = useState(false);

    const [userEmail, setUserEmail] = useState('');
    const [validEmail, setValidEmail] = useState(true);
    const [userEmailFocus, setUserEmailFocus] = useState(false);

    const [pwd, setPwd] = useState('');
    const [validPwd, setValidPwd] = useState(true);
    const [pwdFocus, setPwdFocus] = useState(false);

    // const [matchPwd, setMatchPwd] = useState('');
    // const [validMatch, setValidMatch] = useState(false);
    // const [matchFocus, setMatchFocus] = useState(false);

    const [errMsg, setErrMsg] = useState('');
    const [success, setSuccess] = useState(false);

    useEffect(() => {
        userNameRef.current.focus();
    }, [])

    // useEffect(() => {
    //     setValidEmail(USER_EMAIL_REGEX.test(userEmail));
    // }, [userEmail])

    // useEffect(() => {
    //     setValidPwd(PWD_REGEX.test(pwd));
    //     //setValidMatch(pwd === matchPwd);
    // }, [pwd])

    useEffect(() => {
        setErrMsg('');
    }, [userName, userEmail, pwd])

    //END-use states and effects

    const validateEmail = (email) => {
        return String(email)
            .toLowerCase()
            .match(
                /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
            );
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        // if button enabled with JS hack
        //const v1 = USER_REGEX.test(user);
        //const v2 = PWD_REGEX.test(pwd);
        // if (!v1 || !v2) {
        //     setErrMsg("Invalid Entry");
        //     return;
        // }


        if (userName.trim().replace("\\n", '') == '') {
            toast('Name can\'t be empty', {
                type: "error",
                transition: Slide,
                position: "bottom-right",
            })
            return;
        }

        if (userEmail.trim().replace("\\n", '') == '') {
            toast('Email can\'t be empty', {
                type: "error",
                transition: Slide,
                position: "bottom-right",
            })
            return;
        }

        if (!validateEmail(userEmail)) {
            toast('Invaild Email', {
                type: "error",
                transition: Slide,
                position: "bottom-right",
            })
            return;

        }

        if (pwd.trim().replace("\\n", '') == '') {
            toast('Password can\'t be empty', {
                type: "error",
                transition: Slide,
                position: "bottom-right",
            })
            return;
        }

     



        try {
            const response = await axiosPublic.post(REGISTER_URL,
                { name: userName, email : userEmail, password: pwd }
            );
            
            //console.log(JSON.stringify(response?.data));
            //console.log(JSON.stringify(response))

            if( !response?.data?.succ ){
                setErrMsg(response?.data?.public_msg);
                //setErrMsg('Email is not valie');
                return;
            }

            const accessToken = response?.data?.data?.access_token;
            localStorage.setItem('refresh_token', response?.data?.data?.refresh_token)
            const roles = response?.data?.data?.roles;
            setAuth({ name: userName, email: userEmail, roles, accessToken });

            setSuccess(true);
            //clear state and controlled inputs
            setUserName('');
            setUserEmail('');
            setPwd('');
            navigate(from, { replace: true });
            //setMatchPwd('');
        } catch (err) {
            if (!err?.response) {
                setErrMsg('No Server Response');
            } else if (err.response?.status === 409) {
                setErrMsg('Username Taken');
            } else {
                setErrMsg('Registration Failed')
            }
            errRef?.current?.focus();
        }
    }
    const openGoogleSignup = (e)=>{
        window.open('http://laravelpassportreact.com/socialite/login/google', '_self');
    }

    const openLinkedInSignup = (e)=>{
        window.open('http://laravelpassportreact.com/socialite/login/linkedin', '_self');
    }

    return (

        <div className="info-bx">

            <div className="form-logo">
                <img src={`${get_root_url}/assets/images/form-logo.svg`} alt="logo" />
            </div>
            <p ref={errRef} >{errMsg}</p>
            <h2>
                Welcome
            </h2>

            <form  className="form-bx">

                <div className="user">
                    <img src={`${get_root_url}/assets/images/user.svg`} alt="user" />
                    <input 
                        type="text" 
                        className="user-name"
                        id="username"
                        ref={userNameRef}
                        autoComplete="off"
                        onChange={(e) => setUserName(e.target.value)}
                        value={userName}
                        required
                        onFocus={focusAndValid}
                        onBlur={blurAndValid}
                     />
                    <label> Full Name </label>
                </div>

                <div className="user-mail">
                    <img src={`${get_root_url}/assets/images/mail.svg`} alt="email" />
                    <input 
                        type="email"
                        id="username"
                        ref={userEmailRef}
                        autoComplete="off"
                        onChange={(e) => setUserEmail(e.target.value)}
                        value={userEmail}
                        required
                        onFocus={focusAndValid}
                        onBlur={blurAndValid}
                    />
                    <label> Email </label>
                </div>

                <div className="user-pswd">
                    <img src={`${get_root_url}/assets/images/password.svg`} alt="password" />
                    <input 
                        type="password"
                        id="password"
                        onChange={(e) => setPwd(e.target.value)}
                        value={pwd}
                        required
                        onFocus={focusAndValid}
                        onBlur={blurAndValid}
                    />
                    <label htmlFor=""> Password </label>
                    <img onClick={togglePwd} src={`${get_root_url}/assets/images/password-icon.svg`} alt="password eye" class="show-ps" />

                    <img src={`${get_root_url}/assets/images/eye-close.svg`} id="toggleNone" alt="password eye" className="show-ps crs" />
                </div>
                <span className="login-link">Already have an account? <Link to="/login">log in</Link></span>

                {/*<button className="btn btn-primary signup-btn">
                    Sign Up
                </button>*/}
                <button
                    onClick={handleSubmit}  
                    className="btn btn-primary signup-btn" disabled={!validEmail || !validPwd  ? true : false}>
                    Sign Up
                </button>
            </form>

            <p className="sep-or">Or</p>

            <div className="sign-with row">

                <div className="col-md-6 col-6">
                    <a href="javascript:void(0)"  onClick={openGoogleSignup}>
                        <img src={`${get_root_url}/assets/images/google.svg`} alt="google" />
                    </a>
                </div>
                <div className="col-md-6 col-6">
                    <a href="javascript:void(0)"  onClick={openLinkedInSignup}>
                        <img src={`${get_root_url}/assets/images/linkedin.svg`} alt="linkedin" />
                    </a>
                </div>

            </div>
        </div>

    );
}