import {Link, useNavigate} from 'react-router-dom'
import FooterComponent from '../../components/FooterComponent'
import HeaderComponent from '../../components/HeaderComponent'
import { get_root_url } from '../../../utils/utils'
import ContactUsComponent from '../../components/ContactUsComponent'
import { useState, useEffect} from 'react';

import useAuth from '../../../hooks/useAuth';
import { axiosAuthInstance } from '../../../services/http/axios';

import FormPreloaderComponent from '../../components/FormPreloaderComponent';

import  {toast, Slide} from 'react-toastify';

const FETCH_USERS_CV_URL = 'api/users/cv';
const DELETE_USERS_CV_URL = 'api/cv/set/delete-cv';

export default function DashBoardPage(props) {

  const { startLoader, loaderCallback } = props;
  const navigate = useNavigate();
  const { auth } = useAuth();
  const axios = axiosAuthInstance(auth?.accessToken);
  const [userCV, setUserCV] = useState([]);

  const [isDisplayed, setIsDisplayed] = useState('');

  useEffect(async () => {
                
                try {
                    const response = await axios.post( FETCH_USERS_CV_URL );

                    if(!response?.data?.succ) return;

                    setUserCV((prevUserCV)=>{
                        return [ ...response?.data?.user_cvs ]
                    });
                    //END- Fetch UserCV
                   
                } catch (err) {
                    console.log(err);
                }

            },[]);

  const deleteUserCV = async(e) => {
    e.preventDefault();

    if(!window.confirm('Are you sure do you want to delete this template?')){
      return;
    }

    let loaderId = null;    
        
      try {
          loaderId = startLoader(null);

          setIsDisplayed(e.target.dataset.cvId);
          const response = await axios.post(DELETE_USERS_CV_URL,
              { cv_id: e.target.dataset.cvId }
          );

          if( !response?.data?.succ ){
              setIsDisplayed('');
              loaderCallback(response?.data?.public_msg, loaderId, 'error' );
              return;
          }
          setIsDisplayed('');
          loaderCallback(response?.data?.public_msg, loaderId, 'success' );
         
          setUserCV(prevUserCV=>{
            return prevUserCV.filter((item, index)=>{
                return item.id != e.target.dataset.cvId
            })
          })

      } catch (err) {
          setIsDisplayed('');
          if (!err?.response) {
              loaderCallback('No Server Response', loaderId, 'error' );
              
          } else if (err.response?.status === 400) {
              loaderCallback('Missing Username or Password', loaderId, 'error' );
              
          } else if (err.response?.status === 401) {
              loaderCallback('Unauthorized', loaderId, 'error' );
              
          } else {
              loaderCallback('Something went wrong', loaderId, 'error' );
              
          }
          return;

      }
  }

  return (
    <>
    <HeaderComponent />
    <section className='dashBoard'>

        <div className="container-fluid">

            <div className="headBox border-btm">
                <div className="headingNav">
                      <h3>
                          Documents
                      </h3>

                      <nav className="navbar navbar-expand-lg">
                          <div className="" id="">
                              <ul className="navbar-nav me-auto mb-0 dash-board-nav">
                                  <li className="nav-item">
                                      <a className="nav-link active" aria-current="page" href="#">Resume</a>
                                  </li>

                                  <li className="nav-item">
                                      <a className="nav-link" href="#">Cover Letters</a>
                                  </li>
                              </ul>
                          </div>
                      </nav>
                </div>

                <Link className='btn btn-cstm' to={'/resume/templates'} >
                  Create new
                </Link>
            </div>
        

            <div className="resume-bx">
                <div className="row">

                  {
                    userCV.length > 0 
                      && userCV.map((item, index)=>{
                        return (
                            <div className="col-lg-4 col-sm-6">
                          <div className="row">
                              <div className="col-6 edit-hover">
                                <FormPreloaderComponent loadStyle={{
                                                     height: '100%',
                                                    opacity: '0.7',
                                                    top: '50%',
                                                    left: '50%',
                                                    transform: 'translate(-50%,-50%)',
                                                     display: isDisplayed != '' && isDisplayed==item.id ? 'flex' : 'none'
                                                }} />
                                 <div className="blur-p">
                                      <img src={`${get_root_url}/assets/images/Rectangle48.png`} alt="template" />
                                 </div>

                                    <button onClick={e=>{ navigate(`/resume/${item.id}/contact-info`) }} className='btn btn-cstm'>
                                              <span className='edit-icon'>
                                                  <img src={`${get_root_url}/assets/images/edit-btn-white.svg`} alt="edit" />
                                              </span> Edit
                                    </button>
                                  </div>
                                  <div className="col-6">
                                      <div className="resDetails">
                                         <div className="resStatus">
                                              <h4>
                                                  Untitled
                                              </h4>

                                              <span className='updated-detail'>
                                                  Update {item.updated_at} {/*28 February. 17:41*/}
                                              </span>

                                              <div className="editResme">
                                                  <ul>
                                                      <li>
                                                          <span onClick={e=>{ navigate(`/resume/${item.id}/contact-info`) }} className='resOption'>
                                                             <span className='optionIcon'>
                                                                  <img src={`${get_root_url}/assets/images/fi_edit-3.svg`} alt="edit" />
                                                              </span> Edit
                                                          </span>
                                                      </li>

                                                      <li>
                                                          <span className='resOption'>
                                                             <span className='optionIcon'>
                                                                        <img src={`${get_root_url}/assets/images/Share.svg`} alt="edit" />
                                                              </span> Share a link
                                                          </span>
                                                      </li>

                                                      <li>
                                                          <span className='resOption '>
                                                            <span className='optionIcon'>
                                                                  <img src={`${get_root_url}/assets/images/downloadDash.svg`} alt="edit" />
                                                              </span> 

                                                                    <div className="btn-group">
                                                                        <button type="button" className="btn dropdown-toggle dropdown-toggle " data-bs-toggle="dropdown" aria-expanded="false">
                                                                            Download
                                                                        </button>
                                                                        <ul className="dropdown-menu">
                                                                            <li><a className="dropdown-item" href="#">Download DOC <img src={`${get_root_url}/assets/images/downloadDash.svg`} alt="edit" /> </a></li>
                                                                            <li><a className="dropdown-item" href="#">Download PDF <img src={`${get_root_url}/assets/images/downloadDash.svg`} alt="edit" /> </a></li>
                                                                            <li><a className="dropdown-item" href="#">Download TXT <img src={`${get_root_url}/assets/images/downloadDash.svg`} alt="edit" /> </a></li>

                                                                        </ul>
                                                                    </div>
                                                          </span>
                                                      </li>
                                                      
                                                      <li>
                                                          <span className='resOption'>
                                                            <span className='optionIcon'>
                                                                  <img src={`${get_root_url}/assets/images/copy.svg`} alt="edit" />
                                                              </span>  Make a copy 
                                                          </span>
                                                      </li>

                                                      <li>
                                                          <span onClick={deleteUserCV} data-cv-id={item.id} className='resOption'>
                                                            <span className='optionIcon'>
                                                                  <img src={`${get_root_url}/assets/images/delete.svg`} alt="edit" />
                                                              </span>  Delete  
                                                          </span>
                                                      </li>
                                                      
                                                  </ul>
                                              </div>
                                         </div>
                                      </div>
                                  </div>
                              </div>
                          </div>
                          )
                      })
                  }

                    {/*<div className="col-lg-4 col-sm-6">
                        <div className="row">
                            <div className="col-6 edit-hover">
                                <div className="blur-p">
                                    <img src={`${get_root_url}/assets/images/Rectangle48.png`} alt="template" />
                               </div>

                                <button className='btn btn-cstm'>
                                          <span className='edit-icon'>
                                              <img src={`${get_root_url}/assets/images/edit-btn-white.svg`} alt="edit" />
                                          </span> Edit
                                </button>
                            </div>
                            <div className="col-6">
                                <div className="resDetails">
                                   <div className="resStatus">
                                        <h4>
                                            Untitled
                                        </h4>

                                        <span className='updated-detail'>
                                            Update 28 February. 17:41
                                        </span>

                                        <div className="editResme">
                                            <ul>
                                                <li>
                                                    <span className='resOption'>
                                                       <span className='optionIcon'>
                                                            <img src={`${get_root_url}/assets/images/fi_edit-3.svg`} alt="edit" />
                                                        </span> Edit
                                                    </span>
                                                </li>

                                                <li>
                                                    <span className='resOption'>
                                                       <span className='optionIcon'>
                                                                  <img src={`${get_root_url}/assets/images/Share.svg`} alt="edit" />
                                                        </span> Share a link
                                                    </span>
                                                </li>

                                                <li>
                                                    <span className='resOption'>
                                                      <span className='optionIcon'>
                                                            <img src={`${get_root_url}/assets/images/downloadDash.svg`} alt="edit" />
                                                        </span> 

                                                              <div className="btn-group">
                                                                  <button type="button" className="btn dropdown-toggle dropdown-toggle " data-bs-toggle="dropdown" aria-expanded="false">
                                                                      Download
                                                                  </button>
                                                                  <ul className="dropdown-menu">
                                                                      <li><a className="dropdown-item" href="#">Download DOC <img src={`${get_root_url}/assets/images/downloadDash.svg`} alt="edit" /> </a></li>
                                                                      <li><a className="dropdown-item" href="#">Download PDF <img src={`${get_root_url}/assets/images/downloadDash.svg`} alt="edit" /> </a></li>
                                                                      <li><a className="dropdown-item" href="#">Download TXT <img src={`${get_root_url}/assets/images/downloadDash.svg`} alt="edit" /> </a></li>

                                                                  </ul>
                                                              </div>

                                                    </span>
                                                </li>
                                                
                                                <li>
                                                    <span className='resOption'>
                                                      <span className='optionIcon'>
                                                            <img src={`${get_root_url}/assets/images/copy.svg`} alt="edit" />
                                                        </span>  Make a copy 
                                                    </span>
                                                </li>

                                                <li>
                                                    <span className='resOption'>
                                                      <span className='optionIcon'>
                                                            <img src={`${get_root_url}/assets/images/delete.svg`} alt="edit" />
                                                        </span>  Delete  
                                                    </span>
                                                </li>
                                                
                                            </ul>
                                        </div>
                                   </div>
                                </div>
                            </div>
                        </div>
                    </div>*/}

                    <div className="col-lg-4 col-sm-6">
                              <div className="row addtemp-row">
                                  <div className="col-6">

                                      <div className="addTemplate">
                                          <img src={`${get_root_url}/assets/images/addbtn.svg`} alt="" />
                                      </div>

                                  </div>

                                  <div className="col-6">
                                      <div className="resDetails">
                                          <div className="resStatus">
                                              <h4>
                                                  New Resume 
                                              </h4>

                                              <span className='updated-detail'>
                                                  Create a new resume to Smuz your career.
                                                  You are just a few steps away from creating a quick, easy, beautiful and most importantly... FREE resume to enhance your future.
                                              </span>

                                          </div>
                                      </div>
                                  </div>
                              </div>
                    </div>
                </div>
            </div>

                

            <div className="jobTracking">
                <div className="headBox">
                    <h3>
                        Job Tracking
                    </h3>

                    <button className='btn btn-cstm'>
                        Add job
                    </button>
                </div>

                <div className="row">

                  <div className="col-lg-3 col-6">
                      <div className="jCounter">
                        <div className="counterSub">
                            <h5>
                                Whishlist
                            </h5>
                        </div>

                          <h2>
                              0
                          </h2>
                      </div>
                  </div>

                  <div className="col-lg-3 col-6">
                      <div className="jCounter">
                          <div className="counterSub">
                              <h5>
                                  Applied
                              </h5>
                          </div>

                          <h2>
                              0
                          </h2>
                      </div>
                  </div>

                    <div className="col-lg-3 col-6">
                        <div className="jCounter">
                            <div className="counterSub">
                                <h5>
                                    Interview
                                </h5>
                            </div>

                              <h2>
                                  0
                              </h2>
                        </div>
                    </div>

                    <div className="col-lg-3 col-6">
                          <div className="jCounter">
                              <div className="counterSub">
                                  <h5>
                                      Offer
                                  </h5>
                              </div>

                              <h2>
                                  0
                              </h2>
                          </div>
                    </div>
                </div>
            </div>
        </div>

        <FooterComponent/>
    </section>

    <ContactUsComponent/>
      
    </>
  )
}
