import { get_root_url } from "../../utils/utils";
export default function TopTipsFragment( props ) {

    const { tipScrn } = props;
  return (
    <>

          <div className="tipBackgorund d-lg-none" id="tipsFrg">

              {/* back button */}

              <button onClick={tipScrn} className='txt-btn'>
                  <img src={`${get_root_url}/assets/images/chevron.svg`} className='desc-tips' alt="top tips" />
                  Back to information
              </button>

              <div className="topTips">
                  <img src={`${get_root_url}/assets/images/tipssec.svg`} className='desc-tips' alt="top tips" />

                  {/* mobile */}
                  <img src={`${get_root_url}/assets/images/helptips.svg`} className='mobile-tips' alt="top tips" />


                  <h3>
                      Pro tip for an eye catching resume
                  </h3>


                  <ul>
                      <li>
                          For US, UK, or Canada it is strongly recommended NOT to add a photo.
                      </li>
                      <li>
                          Standard European email format based on the following: firstname.lastname@company.com
                      </li>
                      <li>
                          Recuiters are most likley will search your Linkedin profile, Add it.
                      </li>
                      <li>
                          Photo - Make sure to show the head and shoulders only, don't use filters (It's not Instagram)
                      </li>
                  </ul>

                  <button onClick={tipScrn} className='btn btn-cstm'>
                      Got it
                  </button>

              </div>
          </div>

    </>
  )
}
