import { useRef, useState, useEffect } from 'react';

import useAuth from '../../hooks/useAuth';

//import {useStyles} from '../../styles/styles'; 
import { Link, useNavigate, useLocation } from 'react-router-dom';
import {get_root_url} from '../../utils/utils';

import axiosPublic from '../../services/http/axios';
import { toast, Slide } from 'react-toastify';

import FormPreloaderComponent from './FormPreloaderComponent';
import PreloaderComponent from './PreloaderComponent';

const LOGIN_URL = '/api/users/login'; 

export default function LoginComponent(props){

    const { blurAndValid, focusAndValid, togglePwd, loaderCallback, startLoader } = props;

    const openGoogleSignup = (e)=>{
        window.open('http://laravelpassportreact.com/socialite/login/google', '_self');
    }

    const openLinkedInSignup = (e)=>{
        window.open('http://laravelpassportreact.com/socialite/login/linkedin', '_self');
    }

    // const updateLabelOnFocus = e =>{
    //     let label = e.target.nextElementSibling;
    //     label.classList.add('to-top');
    //     console.log(label);
    // }

    // const updateLabelOnBlur = e =>{
        
    //     console.log('input value', e.target.value);
    //     if( e.target.value.trim().replace("\\n", '') != '' ) return;

    //     let label = e.target.nextElementSibling;
    //     console.log(label);
    //     label.classList.remove('to-top'); 
    // }


    const { setAuth } = useAuth();

    const navigate = useNavigate();
    const location = useLocation();
    const from = location.state?.from?.pathname || "/dashboard";
    //const from = '/dashboard';

    const userRef = useRef();
    //const errRef = useRef();
    
    const formRef = useRef();
    const formHeight = useRef('0');
    const [fH, setFH] = useState('0');
    const [isDisplayed, setIsDisplayed] = useState('none');
    
    const [user, setUser] = useState('');
    const [pwd, setPwd] = useState('');
    //const [errMsg, setErrMsg] = useState('');

    useEffect(() => {
        userRef.current.focus();
        formHeight.current = formRef.current.offsetHeight;
    }, [])

    useEffect(() => {
        setFH(parseInt(formRef.current.offsetHeight, 10)+20);
    }, [formHeight])

    useEffect(() => {
        //setErrMsg('');
    }, [user, pwd])
    
    const validateEmail = (email) => {
        return String(email)
            .toLowerCase()
            .match(
                /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
            );
    };

    const handleSubmit = async (e) => {
        e.preventDefault();

        if (user.trim().replace("\\n", '') == '' ){
            toast('Email can\'t be empty', {
                type:"error",
                transition: Slide,
                position: "bottom-right",
            } )
            return;
        }

        if (!validateEmail(user)) {
            toast('Invaild Email', {
                type: "error",
                transition: Slide,
                position: "bottom-right",
            })
            return;

        }

        if (pwd.trim().replace("\\n", '') == '') {
            toast('Password can\'t be empty', {
                type: "error",
                transition: Slide,
                position: "bottom-right",
            })
            return;
        }
        let loaderId = null;
        try {

            loaderId = startLoader(e);
            setIsDisplayed('flex');
            //loadStyle.display = "flex";
            const response = await axiosPublic.post(LOGIN_URL,
                { email: user, password: pwd }
            );

            setIsDisplayed('none');

            if( !response?.data?.succ ){
                console.log(response.data.public_msg);
                loaderCallback(response.data.public_msg, loaderId, 'error');
                //setErrMsg(response?.data?.public_msg);
                //setErrMsg('Email is not valie');
                return;
            }

            loaderCallback(response.data.public_msg, loaderId, 'success');
            const accessToken = response?.data?.data?.access_token;
            localStorage.setItem('refresh_token', response?.data?.data?.refresh_token)
            const roles = response?.data?.data?.roles;
            setAuth({ user, roles, accessToken });

            setUser('');
            setPwd('');
            //console.log('Location state pathname', location.state?.from?.pathname);
            navigate(from, { replace: true });
        } catch (err) {
            if (!err?.response) {
                loaderCallback('No Server Response', loaderId, 'error');
                //setErrMsg('No Server Response');
            } else if (err.response?.status === 400) {
                loaderCallback('Missing Username or Password', loaderId, 'error');
                //setErrMsg('Missing Username or Password');
            } else if (err.response?.status === 401) {
                loaderCallback('Unauthorized', loaderId, 'error');
                //setErrMsg('Unauthorized');
            } else {
                loaderCallback('Login Failed', loaderId, 'error');
                //setErrMsg('Login Failed');
            }
            //errRef.current.focus();
        }
    }

    




    // const togglePassword = document.querySelector("#togglePassword");
    // const password = document.querySelector("#password");

    // togglePassword.addEventListener("click", function () {
    //     // toggle the type attribute
    //     const type = password.getAttribute("type") === "password" ? "text" : "password";
    //     password.setAttribute("type", type);

    //     // toggle the icon
    //     this.classList.toggle("bi-eye");
    // });

    // prevent form submit
    // const form = document.querySelector("form");
    // form.addEventListener('submit', function (e) {
    //     e.preventDefault();
    // });

    return ( 

    
        <div className="info-bx">

            <div className="form-logo">
                <img src={`${get_root_url}/assets/images/form-logo.svg`} alt="logo"/>
            </div>

            <h2>
                Hi Sean
            </h2>

            {/*<p ref={errRef} className={errMsg ? "errmsg" : "offscreen"} aria-live="assertive">{errMsg}</p>*/}

           <form ref={formRef} style={{position: "relative"}} className="form-bx">
                {/*<PreloaderComponent loadStyle={{    position: "absolute",
                                                    top: "-7px",
                                                    left: "0px",
                                                    width: "100%",
                                                    opacity: "0.7",
                                                    minHeight: "unset",
                                                    display: isDisplayed}} />*/}
                <FormPreloaderComponent loadStyle={{
                                                     height: `${fH}px`,
                                                     display: isDisplayed ,

                                                }} />
                <div className="user-mail">
                    <img src={`${get_root_url}/assets/images/mail.svg`} alt="email" />
                    <input 
                        type="email" 
                        onBlur={blurAndValid} 
                        onFocus={focusAndValid}
                        name='email'
                        id="username"
                        ref={userRef}
                        autoComplete="off"
                        onChange={(e) => setUser(e.target.value)}
                        value={user}
                    />
                    <label className='not-required'>Email</label>
                </div>

                <div className="user-pswd">
                    <img src={`${get_root_url}/assets/images/password.svg`} alt="password"/>
                    <input 
                        type="password"
                        onBlur={blurAndValid}
                        onFocus={focusAndValid} 
                        name='password'
                        id="password"
                        onChange={(e) => setPwd(e.target.value)}
                        value={pwd}
                    />
                     <label >Password</label> 
                    <img onClick={togglePwd} src={`${get_root_url}/assets/images/password-icon.svg`} id="togglePassword" alt="password eye" className="show-ps"/>

                    <img  src={`${get_root_url}/assets/images/eye-close.svg`} id="toggleNone" alt="password eye" className="show-ps crs" />
                </div>

                <span className="forgot-link"><Link to="/forgot"> Forgot password? </Link></span>

                <span className="forgot-link last-child"><Link to="/register"> Create a new account </Link></span>

                <button
                    onClick={handleSubmit } 
                    className="btn btn-primary signup-btn"
                >
                    Log in
                </button>
           </form>

           <p className="sep-or">Or</p>

           <div className="sign-with row">

                <div className="col-md-6 col-6">
                    <a href="javascript:void(0)" onClick={openGoogleSignup}>
                        <img src={`${get_root_url}/assets/images/google.svg`} alt="google"/>
                    </a>
                </div>
                <div className="col-md-6 col-6">
                    <a href="javascript:void(0)" onClick={openLinkedInSignup}>
                        <img src={`${get_root_url}/assets/images/linkedin.svg`} alt="linkedin"/>
                    </a>
                </div>

           </div>
        </div>
        
    );
}