import React from 'react'
import { get_root_url } from '../../utils/utils';
import useLang from '../../hooks/useLang';

export default function UserStatusComponent() {

    const { t } = useLang();

  return (
    <>

    <div className="row">

        <div className="col-md-12">

            <div className="status-bx">
                

            <div className="row">

                      <div className="col-md-4 col-sm-6 User-status-bx">
                          <div className="cur-plan">
                              <h3 className='lang-select'>
                                  <img src={`${get_root_url}/assets/images/currentstatus.svg`} alt="" />
                                      {t('cur_plan')}: <span>{t('free')}</span>
                              </h3>
                          </div>
                      </div>

                      <div className="col-md-4 col-sm-6 User-status-bx">
                          <div className="registered-date">
                              <h3 className='lang-select'>
                                  <img src={`${get_root_url}/assets/images/regidate.svg`} alt="" />
                                      {t('registered_on')}: <span>02/09/22</span>
                              </h3>
                          </div>
                      </div>

                      <div className="col-md-4 col-sm-6 User-status-bx">
                          <div className="cur-user">
                              <h3>
                                  <img src={`${get_root_url}/assets/images/cur-user.svg`} alt="" />
                                      {t('free_user')}
                              </h3>
                          </div>
                      </div>   
                    
            </div> 


        </div>

      </div>
    </div>
      
    </>
  )
}
