import { useNavigate, useParams } from "react-router-dom";
import { useEffect } from "react";
import PreloaderComponent from './PreloaderComponent'

export default function SocialLoginComponent(){
	
	const { refresh_token } = useParams();
	const navigate = useNavigate();

	useEffect(() => {
        let isMounted = true;

        console.log('rf', refresh_token);
        console.log('Window location pathname', window.location);
        //insertAuth();
        localStorage.setItem('refresh_token', window.location.pathname?.split('/').reverse()[0]);
        navigate('/dashboard', { replace: true });
        
        return () => isMounted = false;
    }, [])

	return (
		<>
            < PreloaderComponent />
        </>
		)
}