export default function PreloaderComponent(props) {
  return (
    <>

          <div  style={props.loadStyle ? props.loadStyle : {}} className="preloader-container">

              <svg className="loader" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 340 340">
                  <circle cx="170" cy="170" r="160" stroke="#891AFF" />
                  <circle cx="170" cy="170" r="135" stroke="#4890FF" />
                  <circle cx="170" cy="170" r="110" stroke="#891AFF" />
                  <circle cx="170" cy="170" r="85" stroke="#4890FF" />
              </svg>

          </div>

      
    </>
  )
}
    