import { createContext, useState, useReducer } from "react";
import { CV_ACTIONS } from './reducers/cVActions';

import axios from '../services/http/axios';

const CVDataContext = createContext({});

const initialState = {

	template:{},
	profileInfo:{},
	experience: [],
	education: [],
	skills: [],
	languages: [],
	objective: '',
	customSections:[]
};


const cVReducer = ( state, action )=>{
	console.log('Changed state', state );
	switch( action.type ){
		case CV_ACTIONS.INIT_CV :
			
			return {...state, cv_id: action.payload.cv_id, template: { template_id: action.payload.temp_id }};

		case CV_ACTIONS.INSERT_PROFILE_INFO :

			return {...state, profileInfo: {...action.payload} };

		case CV_ACTIONS.INSERT_EXPERIENCE :

			return { ...state, experience: [...action.payload] };

		case CV_ACTIONS.INSERT_EDUCATION :

			return { ...state, education: [...action.payload] };

		case CV_ACTIONS.INSERT_SKILLS :

			return {...state, skills: [...action.payload.skills], languages: [...action.payload.languages] };

		case CV_ACTIONS.FILL_CV_DATA :

			return {
					...state, 
					cv_id: action.payload?.cv_id, 
					template: { 
								template_id: action.payload?.template_id 
							},
					...action.payload?.cvData
				};

		default:
			return state;
	}
	
}


