const CUSTOM_SECTION_ACTIONS = {
    INIT_CUSTOM_SECTION_DETAILS : "init_custom_section_details",
    INSERT_CUSTOM_SECTION_DETAILS : "insert_custom_section_details",
    UPDATE_CUSTOM_SECTION_DETAILS : "update_custom_section_details",
    REMOVE_CUSTOM_SECTION_DETAILS : "remove_custom_section_details",
};




const customSectionReducer = ( state, action )=>{
    let currentState = state;
    switch( action.type ){
        
        case CUSTOM_SECTION_ACTIONS.INIT_CUSTOM_SECTION_DETAILS :
            return [ ...action.payload ];
        
        case CUSTOM_SECTION_ACTIONS.INSERT_CUSTOM_SECTION_DETAILS :
                return [ ...state,  action.payload];

        case CUSTOM_SECTION_ACTIONS.REMOVE_CUSTOM_SECTION_DETAILS :
                return currentState.filter((item, index)=>{
                    if( action.payload.item != item.item ){
                        return item
                    }
                });
        default :
            return state;
    }
}

export const CVDataProvider = ({ children }) => {
 
	const [cVData, dispatch] = useReducer(cVReducer, initialState );
	const [ educationData, educationDispatch ] = useReducer( educationReducer, cVData.education );
	const [ experienceData, experienceDispatch ] = useReducer( experienceReducer, cVData.experience );
	const [ objectiveData, objectiveDispatch ] = useReducer( objectiveReducer, cVData.objective );
	const [ skillsData, skillsDispatch ] = useReducer( skillsReducer, cVData.skills );
	const [ languagesData, languagesDispatch ] = useReducer( languagesReducer, cVData.languages );
	const [ profileData, profileDispatch ] = useReducer( profileReducer, cVData.profileInfo );
    const [ customSectionData, customSectionDispatch ] = useReducer( customSectionReducer, cVData.customSections );

    return (
        <CVDataContext.Provider value={{ cVData, dispatch, CV_ACTIONS,
         educationData, educationDispatch, EDUCATION_ACTIONS,
         experienceData, experienceDispatch, EXPERIENCE_ACTIONS,
         objectiveData, objectiveDispatch, OBJECTIVE_ACTIONS,
         skillsData, skillsDispatch, SKILLS_ACTIONS,
         languagesData, languagesDispatch, LANGUAGES_ACTIONS,
         profileData, profileDispatch, PROFILE_ACTIONS, 
          customSectionData, customSectionDispatch, CUSTOM_SECTION_ACTIONS  }}>
            {children}
        </CVDataContext.Provider>
    )
}

export default CVDataContext;