import { Outlet } from "react-router-dom";
import { CVDataProvider } from './context/CVDataProvider';
import { LangProvider } from './context/LangProvider';

const CVDataExposer = () => { 

    return (
    	<LangProvider>
	    	< CVDataProvider >
	    		<Outlet />
	    	</ CVDataProvider >
    	</LangProvider>
    )
}

export default CVDataExposer;