import React, { useState, useRef } from 'react';
//import {useStyles} from '../../styles/styles';
import { useNavigate, Link } from 'react-router-dom';
import { get_root_url } from '../../utils/utils';
import { axiosPublic as axios } from '../../services/http/axios';
import { toast, Slide } from 'react-toastify';

const FORGOT_API_URL = "api/users/security/forgot-password";

export default function ForgotComponent( props ) {
    
    const { blurAndValid, focusAndValid, togglePwd, notify } = props;



    const [resp, setResp] = useState('');
    const emailInput  = useRef();


    const inputValid = () =>{
        emailInput.current.focus();
    }

    const validateEmail = (email) => {
      return String(email)
        .toLowerCase()
        .match(
          /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        );
    };

    const handleSubmit = () => {

        let emailData = emailInput.current.value;

        if (emailData.trim().replace("\\n", '') == '') {
            toast('Email can\'t be empty', {
                type: "error",
                transition: Slide,
                position: "bottom-right",
            })
            return;
        }
        if ( !validateEmail(emailData) ){
            toast('Invaild Email', {
                type: "error",
                transition: Slide,
                position: "bottom-right",
            })
            return;

        }
        //TODO- Validate Email
        //console.log(btn);
        
        if( emailData.trim().replace("\\n", '') === '' ){
            console.log('Email can\'t be empty');
            return;
        }

        if( !validateEmail(emailData) ){
            console.log('Please enter a valid email');
            return;
        }

        axios.post(FORGOT_API_URL, {email: emailData} )
            .then((response)=>{
                console.log('response', response.data.succ);

                if( response.data.succ ){
                    setResp(response.data.public_msg);
                    emailInput.current.value = '';
                    console.log('Success', response);
                }else{
                    console.log('Failure', response);
                }
            })
            .catch((error)=>{

            });




    }

    return (



        <div className="info-bx">

            <div className="form-logo">
                <img src={`${get_root_url}/assets/images/form-logo.svg`} alt="logo" />
            </div>

            <h2>
                New password {resp}
            </h2>


            <div className="form-bx">

                <div className="user-mail">
                    <img src={`${get_root_url}/assets/images/mail.svg`} alt="email" />
                    <input ref={emailInput} type="email" name="password" required
                        onFocus={focusAndValid}
                        onBlur={blurAndValid}
                    />
                    <label>Email</label>
                </div>

                <button onClick={handleSubmit} className="btn btn-primary signup-btn">
                    Reset
                </button>
            </div>

            <p className="sep-or">Or</p>

            <div className="sign-with row">

                <div className="col-md-6 col-6">
                    <a href="#">
                        <img src={`${get_root_url}/assets/images/google.svg`} alt="google" />
                    </a>
                </div>
                <div className="col-md-6 col-6">
                    <a href="#">
                        <img src={`${get_root_url}/assets/images/linkedin.svg`} alt="linkedin" />
                    </a>
                </div>

            </div>
        </div>

    );
}