import { useRef, useState, useEffect } from 'react'
import { get_root_url } from '../../utils/utils'
import { Editor } from '@tinymce/tinymce-react';

import useCVData from '../../hooks/useCVData';
import useAuth from '../../hooks/useAuth';

import customAsyncDispatcher from '../../hooks/customAsyncDispatcher';

import FormPreloaderComponent from './FormPreloaderComponent';

import { useNavigate, Link } from "react-router-dom";
import useLang from '../../hooks/useLang';


export default function SkillComponent(props) {

    const {t} = useLang();

    //START STATE
    const { blurAndValid, focusAndValid, togglePwd, startLoader, loaderCallback, checkIfNotEmpty } = props;
    
    const {cVData, dispatch, CV_ACTIONS,
        skillsData, skillsDispatch, SKILLS_ACTIONS,
    languagesData, languagesDispatch, LANGUAGES_ACTIONS} = useCVData();
    const { auth } = useAuth();

    
    const [skills, setSkills] = useState(()=>{
                                               return cVData?.skills ? cVData.skills : []
                                              });
    const [languages, setLanguages] = useState(()=>{
                                               return cVData?.languages ? cVData.languages : []
                                              });
    
    //END- STATE  
    const navigate = useNavigate();

    const editorRef = useRef(null);
    
    const [EndDate, setEndDate] = useState('');

    const formRef = useRef();
    const formHeight = useRef('0');
    const [fH, setFH] = useState('0');
    const [isDisplayed, setIsDisplayed] = useState('none');
    
    useEffect(() => {
        formHeight.current = formRef.current.offsetHeight;
            //focusAndValid()

        document.querySelectorAll('input').forEach((item)=>{
        checkIfNotEmpty(item);
        });
    }, [])

    useEffect(() => {
        setFH(parseInt(formRef.current.offsetHeight, 10)+20);
    }, [formHeight])

    const handleSkillForm = async (e) =>{
        e.preventDefault();


        let req = { cv_id: cVData?.cv_id, skills: skillsData, languages: languagesData };
        
        setIsDisplayed('flex');

        let response = await customAsyncDispatcher(CV_ACTIONS.INSERT_SKILLS, req, auth);
        if(!response){
            setIsDisplayed('none');
            return;
        }
        setIsDisplayed('none');
        dispatch({type: CV_ACTIONS.INSERT_SKILLS, payload: req }); 
        navigate(`/resume/${cVData?.cv_id}/objective`, {replace: true});  
      //}
   }

    return (
        <>
        
            <div className="profile-info-bx">
                <h3>
                    {t('skills')} <img src={`${get_root_url}/assets/images/fi_edit-3.svg`} alt="Skills" />
                </h3>

                <form ref={formRef} className="profile-form skill-lang">

                    <FormPreloaderComponent loadStyle={{
                                                     height: `${fH}px`,
                                                     display: isDisplayed 
                                                }} />

                    <div className="row">

                        <div className="col-md-12">
                            <div className="row">
                                <div className="title col-sm-8 pi-input">

                                    <input 
                                        type="text"
                                        onBlur={blurAndValid}
                                        onFocus={focusAndValid}
                                         />

                                    <label> {t('skill_name')} </label>
                                </div>

                                <div className="com-input col-sm-4 pi-input">
                                    <button type="button"
                                        onClick={e=>{ 
                                            e.preventDefault();
                                                    console.log( 'skills',  skills);
                                                    let enteredSkill = e?.target?.parentNode?.previousElementSibling?.children[0]?.value;
                                                    if( enteredSkill && !skillsData.find( skill => {
                                                        return skill.item == enteredSkill
                                                    } ) ){
                                                        skillsDispatch( {type: SKILLS_ACTIONS.INSERT_SKILLS_DETAILS, payload: { id: null, item: enteredSkill } } )
                                                        //setSkills( prevSkills => [...prevSkills, { id: null, item: enteredSkill } ] );
                                                        e.target.parentNode.previousElementSibling.children[0].value = '';
                                                    } 
                                            }} 
                                        className='btn btn-cstm add-btn'>{t('add')}</button>
                                </div>
                                
                                <div className="col-md-12">

                                    { 
                                         skillsData.length > 0
                                            ? skillsData.map( ( item )=>{
                                              return ( <div key={item.id} className="skill-bx">
                                                    <span>{item.item}</span>
                                                    <span 
                                                        data-item-id={item.id} 
                                                        onClick = {
                                                            
                                                            e =>{
                                                                e.preventDefault();
                                                                skillsDispatch( {type: SKILLS_ACTIONS.REMOVE_SKILLS_DETAILS, payload: item  } )
                                                             //setSkills( prevSkills=> prevSkills.filter( arr_item=>arr_item != item ) )
                                                         }
                                                        } 
                                                        className='clear-btn'>
                                                        <svg class="MuiSvgIcon-root sc-fo1jmi-22 bTXVAa" focusable="false" viewBox="0 0 24 24" aria-hidden="true"><path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z"></path></svg>    
                                                    </span>
                                                </div> )
                                            } )
                                            : null

                                    }

                                </div>
                            </div>    
                        </div>


                        <div className="col-md-12 addlang">

                            <h3>
                                {t('lang')} <img src={`${get_root_url}/assets/images/fi_edit-3.svg`} alt="Languages" />
                            </h3>

                            <div className="row">
                                <div className="title col-sm-8 pi-input">
                                    <input 

                                        onBlur={blurAndValid}
                                        onFocus={focusAndValid}
                                        type="text"  />

                                    <label> {t('lang_name')} </label>
                                </div>

                                <div className="com-input col-sm-4 pi-input">
                                    <button 
                                        onClick={e=>{
                                                    e.preventDefault(); 
                                                    let enteredLanguage = e?.target?.parentNode?.previousElementSibling?.children[0]?.value;
                                                    if( enteredLanguage && !languagesData.find( language=>{
                                                        return language.item == enteredLanguage
                                                    } ) ){
                                                        languagesDispatch( {type: LANGUAGES_ACTIONS.INSERT_LANGUAGES_DETAILS, payload: { id: null, item: enteredLanguage } } )
                                                        //setLanguages( prevLanguages => [...prevLanguages, { id:null, item: enteredLanguage } ] );
                                                        e.target.parentNode.previousElementSibling.children[0].value = '';
                                                    } 
                                            }}
                                        className='btn btn-cstm add-btn'>{t('add')}</button>
                                </div>
                                    
                                <div className="col-md-12">

                                    { 
                                         languagesData.length > 0
                                            ? languagesData.map( ( item )=>{
                                              return ( <div key={item.id} className="skill-bx">
                                                    <span>{item.item}</span>
                                                    <span 
                                                        data-item-id={item.id}
                                                        onClick = {
                                                            e => {
                                                                e.preventDefault();
                                                                languagesDispatch( {type: LANGUAGES_ACTIONS.REMOVE_LANGUAGES_DETAILS, payload: item } )
                                                                //setLanguages( prevLanguages=> prevLanguages.filter( arr_item=>arr_item != item ) )
                                                            }
                                                        } 
                                                        className='clear-btn'>
                                                        <svg class="MuiSvgIcon-root sc-fo1jmi-22 bTXVAa" focusable="false" viewBox="0 0 24 24" aria-hidden="true"><path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z"></path></svg>    
                                                    </span>
                                                </div> )
                                            } )
                                            : null

                                    }

                                </div>
                            </div>
                        </div>
                        
                    </div>

                    <div className="row btm-btn-sec expsec">

                        <div className="col-sm-4 col-6 btn-mt">
                            <Link to={`/resume/${cVData.cv_id}/education`} >
                                <div className='btn bk-edits-btn'>
                                    Back
                                </div>
                            </Link>
                        </div>

                        <div className="col-sm-4 col-6 btn-mt">
                            <div onClick={handleSkillForm} className='btn btn-cstm'>
                                Save
                            </div>
                        </div>
                    </div>

                </form>


            </div>

        </>
    )
}
