import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

import HeaderComponent from '../../components/HeaderComponent';
import StepsComponent from '../../components/StepsComponent';

import TemplatesComponent from '../../components/TemplatesComponent';

import FooterComponent from '../../components/FooterComponent';

import PlanComponent from '../../components/PlanComponent'
import UserStatusComponent from '../../components/UserStatusComponent';

import FreeUserFragment from '../../fragments/FreeUserFragment';

import './../../../styles/components.css';
import { get_root_url } from '../../../utils/utils';


import {axiosAuthInstance} from '../../../services/http/axios';

import useAuth from '../../../hooks/useAuth';

import useCVData from '../../../hooks/useCVData';

import customAsyncDispatcher from '../../../hooks/customAsyncDispatcher'; 

import TemplatePopupComponent from '../../components/TemplatePopupComponent';

import { toast, Slider } from 'react-toastify';

import useLang from '../../../hooks/useLang';

const TEMPLATES_FETCH_URL = 'api/template/templates';
const CATEGORIES_FETCH_URL = 'api/template/categories';

let allTemplates = [];

function TemplatePage(props) {

    const {startLoader, loaderCallback } = props;
    const {cVData, dispatch, CV_ACTIONS} = useCVData();

    const { t } = useLang();

    const {auth} = useAuth();

    const axios = axiosAuthInstance(auth?.accessToken);
    
    const [templates, setTemplates] = useState([]);

    const [ categories, setCategories ] = useState([]);

    const navigate = useNavigate();

    const [fH, setFH] = useState('0');
    const [isDisplayed, setIsDisplayed] = useState('none');

    const initCV = async (e) =>{
        //console.log('element', e.target);
        let temp_id = e?.target?.dataset?.templateId;
        if( temp_id ){
            //console.log('tempid ', temp_id);

            // notify: (e) => {
            //     console.log('hii');

            //     let id = toast.loading("Please Wait", {
            //         position: "bottom-right",
            //         // theme: 'colored',
            //         // theme: 'dark',
            //         transition: Slide
            //         // transition: "slide",
            //         // options: {
            //         //     transition: 'slide'
            //         // }
            //     });
            // }
            let response = await customAsyncDispatcher(CV_ACTIONS.INIT_CV, temp_id, auth);
            //console.log(response);
            
            if(!response){

                return;
            }
            //console.log('cv_init data', response);
            dispatch({type: CV_ACTIONS.INIT_CV, payload: { temp_id , cv_id: response?.cv_id } });
            navigate(`/resume/${ response?.cv_id }/contact-info`, { replace: true });
        }
    }

    useEffect(
            async () => {
                
                try {
                    //START- Fetch Categories
                    const cat_response = await axios.post(CATEGORIES_FETCH_URL);

                    if(!cat_response?.data?.succ) return;
                    //allTemplates = response.data.data;
                    setCategories((prevCategories)=>{
                        return [ ...cat_response?.data?.data ]
                    });
                    //END- Fetch Categories
                   
                } catch (err) {
                    console.log(err);
                }

                try {
                    //START- Fetch Templates

                    const temp_response = await axios.post(TEMPLATES_FETCH_URL);

                    if(!temp_response?.data?.succ)  return;

                    allTemplates = temp_response.data.data;
                    setTemplates((prevTemplates)=>{
                        return [ ...temp_response?.data?.data ]
                    });
                    //END- Fetch Templates
                } catch (err) {
                    console.log(err);
                }
            },
        []);

    const handleCheckTemplates = async(e)=>{
        //console.log( 'cat id', e?.target?.dataset?.catId);
        let cat_id = e?.target?.dataset?.catId;
        if( !cat_id || cat_id === 'none' ){
            setTemplates( (prevTemplates)=>{
                        return [...allTemplates];
                    } );
            return;
        }
        try {
                setIsDisplayed('flex');
                //console.log('height', fH);
                const response = await axios.post(TEMPLATES_FETCH_URL,
                    { cv_cat_id: cat_id }
                );

                if( !response.data.succ ){
                    setIsDisplayed('none');
                    throw response.data.public_msg
                }
                setIsDisplayed('none');
                setTemplates((prevTemplates)=>{

                    return [ ...response.data.data ]
                });

            } catch (err) {
                console.log(err);
            }
    }

    return (
        <>

        <HeaderComponent />

            <section className="template-page">
               
                <TemplatePopupComponent />

                <div className="container-fluid">

                    <StepsComponent step={'1'}/>
                    
                    <h2 className="lang-select main-heading">

                        {t('cv_templates')}

                    </h2>

                    <ul className="nav nav-tabs justify-content-center" id="myTab" role="tablist">
                            
                        <li className="nav-item" role="presentation">
                            <button onClick={handleCheckTemplates} data-cat-id="none" className="nav-link active" id="all-temp" data-bs-toggle="tab" data-bs-target="#all-templates" type="button" role="tab" aria-controls="all-templates" aria-selected="true">All</button>
                        </li>

                        {
                            categories?.length > 0 
                                ?
                                   categories.map( ( item )=>{
                                       //console.log('item', item)
                                       return (
                                       <li className="nav-item" role="presentation">
                                            <button 
                                                className="nav-link" 
                                                id={item.slug+"-tem"} 
                                                data-bs-toggle="tab" 
                                                data-bs-target={"#"+item.slug+"-templates"} 
                                                data-cat-id={item.id}
                                                type="button" 
                                                role="tab" 
                                                aria-controls= {item.slug+"-templates"} 
                                                aria-selected="false"
                                                onClick={handleCheckTemplates}
                                                >
                                                {item.title}
                                            </button>
                                        </li>
                                       )
                                   } )
                                   :
                                   null
                               
                        }

                    </ul>
                    
                        <div className="tab-pane fade show active" id="all-templates" role="tabpanel" aria-labelledby="all-temp">
                            
                            <div className="row">

                                <TemplatesComponent templates={templates} initCV={initCV} fH={fH} setFH={setFH} isDisplayed={isDisplayed} {...props} />
                           
                            </div>
                            
                        </div>

                </div>

            </section>

            <section className='price-sec'>
                
                <div className="container-fluid">

                    <h2>
                        {t('p_info')}
                    </h2>

                    <UserStatusComponent />

                    <PlanComponent />

                </div>

                
                
            </section>

            <FooterComponent/>
        </>
    )
}

