                        //     <button 
                        //         className="nav-link" 
                        //         id="formal-tem" 
                        //         data-bs-toggle="tab" 
                        //         data-bs-target="#formal-templates" 
                        //         data-cat-id="7"
                        //         type="button" 
                        //         role="tab" 
                        //         aria-controls="formal-templates" 
                        //         aria-selected="false"
                        //         onClick={handleCheckTemplates}
                        //         >
                        //         Formal
                        //     </button>
                        // </li>
                        
                        // <li className="nav-item" role="presentation">
                        //     <button onClick={handleCheckTemplates} data-cat-id="8" className="nav-link" id="formal-des" data-bs-toggle="tab" data-bs-target="#formal-des-temp" type="button" role="tab" aria-controls="formal-des-temp" aria-selected="false">Formally designed</button>
                        // </li>

                        // <li className="nav-item" role="presentation">
                        //     <button onClick={handleCheckTemplates} data-cat-id="9" className="nav-link" id="designed-temp" data-bs-toggle="tab" data-bs-target="#designed-template" type="button" role="tab" aria-controls="designed-template" aria-selected="false">Designed</button>
                        // </li>
                        
                        // <li className="nav-item" role="presentation">
                        //     <button onClick={handleCheckTemplates} data-cat-id="10" className="nav-link" id="pic-temp" data-bs-toggle="tab" data-bs-target="#with-pic" type="button" role="tab" aria-controls="with-pic" aria-selected="false">With photo </button>
                        // </li>

export default TemplatePage    