import React from 'react'
import { get_root_url } from '../../utils/utils'

export default function MobileHeader() {
  return (
    <>
          <nav className="navbar mobileCustom navbar-expand-lg d-md-none">
              <div className="container-fluid">
                  <a className="navbar-brand" href="#">
                      <img src={`${get_root_url}/assets/images/form-logo.svg`} alt="logo" />
                  </a>
                  
                  <div className="collapse  show" id="navbarSupportedContent">
                      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                          <li className="nav-item">
                              <a className="nav-link active" aria-current="page" href="#">Log In</a>
                          </li>
                          <li className="nav-item">
                              <a className="nav-link" href="#">Sign Up Free</a>
                          </li>
                      </ul>
                  </div>
              </div>
          </nav>
    </>
  )
}
