import {CV_ACTIONS} from '../context/reducers/cVActions';
import { axiosAuthInstance } from '../services/http/axios';
import { toast, Slide } from 'react-toastify';

const INIT_CV_URL = 'api/cv/set/init';
const INSERT_PROFILE_INFO_URL = 'api/cv/set/contact-info';
const INSERT_EXPERIENCE_URL = 'api/cv/set/experience';
const INSERT_EDUCATION_URL = 'api/cv/set/education';
const INSERT_OBJECTIVE_URL = 'api/cv/set/objective';
const INSERT_SKILLS_URL = 'api/cv/set/skill-lang';
const INSERT_CUSTOM_SECTION_URL = 'api/cv/set/custom-section';

const CUSTOM_SECTION_ACTIONS = {
    INIT_CUSTOM_SECTION_DETAILS : "init_custom_section_details",
    INSERT_CUSTOM_SECTION_DETAILS : "insert_custom_section_details",
    UPDATE_CUSTOM_SECTION_DETAILS : "update_custom_section_details",
    REMOVE_CUSTOM_SECTION_DETAILS : "remove_custom_section_details",
};

let axios = {};

const methods = {
    focusAndValid : e => {
        let label = e.target.nextElementSibling;
        label.classList.add('to-top');
    },
    blurAndValid : e => {

        if (e.target.value.trim().replace("\\n", '') != '') return;
        let label = e.target.nextElementSibling;
        label.classList.remove('to-top');
    },
    togglePwd : (e) => {
        let ele = e.target.previousElementSibling.previousElementSibling;
        ele.getAttribute("type") === "password"
            ? ele.setAttribute("type", "text")
            : ele.setAttribute("type", "password")


    },

    notify : (e) => {
        let loaderId = toast.loading("Please Wait", {
            position: "bottom-right",
            type: 'success',
            // theme: 'colored',
            // theme: 'dark',
            transition: Slide
        });      
    },

    startLoader : (e) =>{
        return toast.loading("Please Wait", {
            position: "bottom-right",
            type: 'success',
            // theme: 'colored',
            // theme: 'dark',
            transition: Slide
        });      
    },
    loaderCallback : (msg, loaderId, type)=>{
        toast.update(loaderId, {
                render: msg,
                type: type,
                isLoading: false,
                position: "bottom-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined
            });
    }
     
};

const initAPICV = async (payload) => {   
    let loaderId=null;
    try {
        loaderId = methods.startLoader(null);

        const response = await axios.post(INIT_CV_URL,
            { cv_template_id: payload }
        );

        if( !response?.data?.succ ){
            methods.loaderCallback(response?.data?.public_msg, loaderId, 'error' );
            return false;
        }
        methods.loaderCallback(response?.data?.public_msg, loaderId, 'success' );
        return { cv_id: response?.data?.cv_id };

	} catch (err) {
        if (!err?.response) {
            methods.loaderCallback('No Server Response', loaderId, 'error' );
        } else if (err.response?.status === 400) {
            methods.loaderCallback('Missing Username or Password', loaderId, 'error' );
        } else if (err.response?.status === 401) {
            methods.loaderCallback('Unauthorized', loaderId, 'error' );
        } else {
            methods.loaderCallback('Something went wrong', loaderId, 'error' );
        }
        return false;
    }
}

