const customAsyncDispatcher = (asyncAction, payload, auth) => {

	axios = axiosAuthInstance(auth?.accessToken);

	switch( asyncAction ){
			case CV_ACTIONS.INIT_CV :
				return initAPICV(payload);
				
			case CV_ACTIONS.INSERT_PROFILE_INFO :
				return insertAPIProfileInfo(payload);

			case CV_ACTIONS.INSERT_EXPERIENCE :
				return insertAPIExperience(payload);

			case CV_ACTIONS.INSERT_EDUCATION :
				return insertAPIEducation(payload);

			case CV_ACTIONS.INSERT_SKILLS :
				return insertAPISkills(payload);

			case CV_ACTIONS.INSERT_OBJECTIVE :
				return insertAPIObjective(payload);

            case CUSTOM_SECTION_ACTIONS.INSERT_CUSTOM_SECTION_DETAILS :
                return insertCustomSection(payload);

		}

} 

export default customAsyncDispatcher;