import { useRef, useState, useEffect } from 'react';

import { useNavigate, useParams, Link } from 'react-router-dom';

import { get_root_url } from '../../utils/utils'
import { Editor } from '@tinymce/tinymce-react';

import useCVData from '../../hooks/useCVData';

import useAuth from '../../hooks/useAuth';

import customAsyncDispatcher from '../../hooks/customAsyncDispatcher';

import FormPreloaderComponent from './FormPreloaderComponent';

import { axiosAuthInstance } from '../../services/http/axios';

export default function AddSectionComponent(props) {

    const { blurAndValid, focusAndValid, togglePwd, startLoader, loaderCallback, checkIfNotEmpty } = props;
    const { auth } = useAuth();
    const axios = axiosAuthInstance(auth?.accessToken);

    const { CUSTOM_SECTION_ACTIONS, customSectionData, customSectionDispatch, 
        cVData, dispatch, CV_ACTIONS } = useCVData();

    const editorRef = useRef(null);
    const {title} = useParams();
    const navigate = useNavigate();

    const [secTitle, setSecTitle] = useState(()=>{
                                        return title ? title : ''
                                    });
    const [isDefault, setIsDefault] = useState(()=>{
                                        return title ? true : false
                                    });

    const formHeight = useRef('0');
    const [fH, setFH] = useState('0');
    const [isDisplayed, setIsDisplayed] = useState('none');
    const [isDescDisplayed, setIsDescDisplayed] = useState('none');

    useEffect(() => {
        document.querySelectorAll('input').forEach((item)=>{
            checkIfNotEmpty(item);
        });
    }, [])

    const handleAddSectionForm = async (e) =>{
        e.preventDefault();
        let req = {cv_id: cVData.cv_id, title: secTitle, description: editorRef.current.getContent(), isDefault }
        let response = await customAsyncDispatcher(CUSTOM_SECTION_ACTIONS.INSERT_CUSTOM_SECTION_DETAILS, req, auth);
        setIsDisplayed('none');
          if(!response){
                setIsDisplayed('none');
              console.log('something went wrong');
              return;
          }
          delete req.cv_id;
          console.log('Hi req', req);
          dispatch({type: CUSTOM_SECTION_ACTIONS.INSERT_CUSTOM_SECTION_DETAILS, payload: req });  
          navigate(`/resume/${cVData?.cv_id}/edit-section`, {replace: true});
    }

    return (
        <>
            <div className="profile-info-bx">
                <h3>
                    Section name
                </h3>

                <form className="profile-form">
                    <div className="row">

                        <div className="title col-sm-12 pi-input">

                            <input 
                            onBlur={blurAndValid}
                            onFocus={focusAndValid}
                            onChange={e=>{ setSecTitle(e.target.value) }}
                            value={secTitle}
                            type="text" id='title' />

                            <label> Title </label>
                        </div>

                        <div className="col-sm-12 pi-input">

                            <div className="desc-head">
                                <p>
                                    Description
                                </p>

                                <button className='btn btn-cstm smal-btn'>
                                    Ai writer working
                                </button>

                                <span className='bk-edits-btn smal-btn outline-btn'>3k</span>
                            </div>

                            <Editor
                                apiKey="rajgntcz3aruwm9gfxfpeogddnbgbznhopgloeursrk6tndt"
                                onInit={(evt, editor) => editorRef.current = editor}
                                initialValue=""
                                init={{
                                    height: 240,
                                    menubar: false,
                                    branding: false,
                                    skin: 'material-classic',
                                    content_css: 'material-classic',
                                    icons: 'material',
                                    selector: 'textarea',  // change this value according to your HTML
                                    //content_style: 'body { margin: 10px; border: 5px solid red; padding: 3px; background: red; }',
                                    //content_css: get_root_url+"assets/css/text-area.css",
                                    statusbar: false,
                                    plugins: [
                                        'lists link print preview',
                                    ],
                                    toolbar:
                                        'bold italic underline | bullist numlist |' +
                                        'alignjustify alignleft aligncenter alignright  ',
                                    content_style: 'body { font-family:Helvetica,Arial,sans-serif; font-size:12px }'
                                }}
                            />

                        </div>

                    </div>

                    <div className="row btm-btn-sec">

                        <div className="col-sm-4 col-6 btn-mt">
                             
                                <Link className='btn bk-edits-btn' to={`/resume/${cVData.cv_id}/more-section`}>
                                Back
                                </Link>
                            
                        </div>

                        <div className="col-sm-4 col-6 btn-mt">
                            
                               <div onClick={handleAddSectionForm} className='btn btn-cstm'>
                                Save
                                </div>
                            
                        </div>
                    </div>




                </form>


            </div>

        </>
    )
}
