import { set } from 'js-cookie';
import { useRef, useState, useEffect } from 'react';
import { get_root_url } from '../../utils/utils';
import { Editor } from '@tinymce/tinymce-react';

import useCVData from '../../hooks/useCVData';
import useAuth from '../../hooks/useAuth';

import customAsyncDispatcher from '../../hooks/customAsyncDispatcher';

import FormPreloaderComponent from './FormPreloaderComponent';

import { useNavigate, Link } from "react-router-dom";

import useLang from '../../hooks/useLang';


let editorElement = null;

export default function ObjectiveComponent(props) {

    const { t } = useLang();


    //START STATE
    const { blurAndValid, focusAndValid, togglePwd, startLoader, loaderCallback, checkIfNotEmpty } = props;
    const { OBJECTIVE_ACTIONS, objectiveData, objectiveDispatch, 
        cVData, dispatch, CV_ACTIONS } = useCVData();
    const { auth } = useAuth();

    const navigate = useNavigate();
    const editorRef = useRef(null);


    const formRef = useRef();
    const formHeight = useRef('0');
    const [fH, setFH] = useState('0');
    const [isDisplayed, setIsDisplayed] = useState('none');
    
    useEffect(() => {
        formHeight.current = formRef.current.offsetHeight;
            //focusAndValid()

        document.querySelectorAll('input').forEach((item)=>{
            checkIfNotEmpty(item);
        });
    }, [])

    useEffect(() => {
        setFH(parseInt(formRef.current.offsetHeight, 10)+20);
    }, [formHeight])

    const handleObjectiveForm = async (e) =>{
          e.preventDefault();
          let req = { cv_id: cVData?.cv_id, objective : editorRef?.current?.getContent() };
          
          setIsDisplayed('flex');

          let response = await customAsyncDispatcher(CV_ACTIONS.INSERT_OBJECTIVE, req, auth);
          if(!response){
              setIsDisplayed('none');
              return;
          }
          setIsDisplayed('none');

          dispatch({type: CV_ACTIONS.INSERT_OBJECTIVE, payload: req }); 
          navigate(`/resume/${cVData?.cv_id}/more-section`, {replace: true});  
          //}
       }
    return (
        <>

            <div className="profile-info-bx">
                <h3>
                    Objectives <img src={`${get_root_url}/assets/images/fi_edit-3.svg`} alt="" />
                </h3>

                <form ref={formRef} className="profile-form">
                    <div className="row">
                        <div className="col-sm-12 pi-input">

                            <div className="desc-head">
                                <p>
                                    Description
                                </p>

                                <div onClick={e=>{editorElement?.setAttribute('contenteditable', false)}} className='btn btn-cstm smal-btn'>
                                    Ai writer working
                                </div>

                                <span className='bk-edits-btn smal-btn outline-btn'>3k</span>
                            </div>

                            <Editor
                                apiKey="rajgntcz3aruwm9gfxfpeogddnbgbznhopgloeursrk6tndt"
                                onEditorChange={content=>objectiveDispatch( {type: OBJECTIVE_ACTIONS.INSERT_OBJECTIVE_DETAILS, payload: content} )}
                                onInit={(evt, editor) => {

                                                    setTimeout(()=>{

                                                        let editorEle = document.querySelector('.tox-edit-area__iframe').contentWindow.document;
                                                        editorEle.head.innerHTML = `<style>
                                                            @import url('https://fonts.googleapis.com/css2?family=Maven+Pro:wght@400;500;600&display=swap');
                                                            
                                                            .typewriter-text {
                                                                                user-select: none;
                                                                                overflow: hidden;
                                                                                vertical-align: middle;
                                                                              animation: typing 3s steps(30, end);
                                                                                white-space: nowrap;
                                                                                
                                                                                color:#8896e1;
                                                                                /*border-right: 4px solid orange;*/
                                                                                box-sizing: border-box;
                                                                            }

                                                                            @keyframes typing {
                                                                                from { 
                                                                                    width: 0% 
                                                                                }
                                                                                to { 
                                                                                    width: 100% 
                                                                                }
                                                                            }
                                                            .mce-content-body {
                                                                font-size: 16px;
                                                                font-weight: 500;
                                                                color: #828282;
                                                                font-family: 'Maven Pro';
                                                            }
                                                        </style>  ${editorEle.head.innerHTML}`;
                                                        editorElement = editorEle.querySelector('#tinymce');
                                                        editorElement.innerHTML = `${editorElement.innerHTML}`;
                                                        editorElement.querySelector('p').innerHTML += `<p class="typewriter-text">Content is changed</p>`;
                                                        //editorElement.setAttribute('contenteditable', false);
                                                    },100);
                                                  return  editorRef.current = editor
                                                }}
                                initialValue={cVData.objective}
                                init={{
                                    height: 240,
                                    menubar: false,
                                    branding: false,
                                    skin: 'material-classic',
                                    content_css: 'material-classic',
                                    icons: 'material',
                                    selector: 'textarea',  // change this value according to your HTML
                                    //content_style: 'body { margin: 10px; border: 5px solid red; padding: 3px; background: red; }',
                                    //content_css: get_root_url+"assets/css/text-area.css",
                                    statusbar: false,
                                    plugins: [
                                        'lists link print preview',


                                    ],
                                    toolbar:
                                        'bold italic underline | bullist numlist |' +
                                        'alignjustify alignleft aligncenter alignright  ',
                                    //content_style: 'body { font-family:Helvetica,Arial,sans-serif; font-size:12px }'
                                }}
                            />

                        </div>

                    </div>

                    <div className="row btm-btn-sec">

                        <div className="col-sm-4 col-6 btn-mt">
                            <Link to={`/resume/${cVData.cv_id}/skill-lang`} >
                                <div className='btn bk-edits-btn'>
                                    Back
                                </div>
                            </Link>
                        </div>

                        <div className="col-sm-4 col-6 btn-mt">
                            <div onClick={handleObjectiveForm} className='btn btn-cstm'>
                                Save
                            </div>
                        </div>
                    </div>




                </form>


            </div>

        </>
    )
}
