import ObjectiveComponent from '../../components/ObjectiveComponent';
import StepsComponent from '../../components/StepsComponent';
import HeaderComponent from '../../components/HeaderComponent';

import DefaultTemplate from '../../templates/DefaultTemplate/DefaultTemplate'
import { get_root_url } from '../../../utils/utils'
import EditRightTab from '../../components/EditRightTab'
import TopTipsFragment from '../../fragments/TopTipsFragment';
import useLang from '../../../hooks/useLang';


import {useEffect} from 'react';

export default function ObjectivePage(props){

  const {tglScreen, tipScrn} = props;

  const { t } = useLang();


	return (

			<>
				<HeaderComponent />		        

				<section className="edit-sec">
	            	<div className="container-fluid">

	                	<div className="row">

                          <div className="col-lg-6 position-relative" id='rEditor-p'>
	                      	
                             <div className="pDiv">
                                  <button onClick={tglScreen} className='btn-cstm tips-btn d-lg-none prev-btn' >
                                  <img src={`${get_root_url}/assets/images/prev-icon.svg`} alt="" />
                                   {t('prev_edit')}

                                </button>


                                <button onClick={tipScrn} className='btn-cstm tips-btn'>
                                  <img src={`${get_root_url}/assets/images/toptips.svg`} alt="" />
                                  {t('top_tips')}
                                </button>


                                <div className="redit-area">
                                  <StepsComponent step={'6'} />
                                  <ObjectiveComponent {...props} tglScreen={tglScreen} tipScrn={tipScrn} />
                                </div>
                              </div>
	                      	</div>

                      <div className="col-lg-6 position-relative mbl-ofh" id='mainPContainer'>

                         <TopTipsFragment {...props} tipScrn={tipScrn}/>

                         <div className=" prev-bx" id='prev-bx'>
                            <div className="preview-container">
                              <DefaultTemplate/>  
                            </div>

                            <div className="edit-block">
                              <EditRightTab tglScreen={tglScreen}/>
                            </div>
                          </div>
                        
                      </div>

		                </div>

		            </div>
		          </section>
			</> 
		)
}