import { toast, Slide } from 'react-toastify';
const TemplateArea = (Component) => (props ) => {

    
    const methods = {

        checkIfNotEmpty : item => {
            //console.log('item is here for focus', item);
            if( item.value.trim() == '' ){
                return;
            }
            let label = item.nextElementSibling;
            if(label !=null && label?.classList !== null){
                label.classList.add('to-top');    
            }
            
        },

        focusAndValid : e => {
            let label = e.target.nextElementSibling;
            label.classList.add('to-top');
        },
        blurAndValid : e => {

            if (e.target.value.trim().replace("\\n", '') != '') return;
            let label = e.target.nextElementSibling;
            label.classList.remove('to-top');
        },
        togglePwd : (e) => {
            let ele = e.target.previousElementSibling.previousElementSibling;
            ele.getAttribute("type") === "password"
                ? ele.setAttribute("type", "text")
                : ele.setAttribute("type", "password")


        },

        notify : (e) => {
            let loaderId = toast.loading("Please Wait", {
                position: "bottom-right",
                type: 'success',
                // theme: 'colored',
                // theme: 'dark',
                transition: Slide
            });      
        },

        startLoader : (e) =>{
            return toast.loading("Please Wait", {
                position: "bottom-right",
                type: 'success',
                // theme: 'colored',
                // theme: 'dark',
                transition: Slide
            });      
        },
        loaderCallback : (msg, loaderId, type)=>{
            toast.update(loaderId, {
                    render: msg,
                    type: type,
                    isLoading: false,
                    position: "bottom-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined
                });
        }
         
    }

    const togMethod = {

        tglScreen: e => {

            let EditScreen = document.querySelector('#rEditor-p');
            let prvScreen = document.querySelector('#prev-bx');
            let Swidth = window.screen.width;
            let mpc = document.querySelector('#mainPContainer');

            if (Swidth < 991) {
                mpc?.classList.toggle('mbl-ofh')
            }


            EditScreen.classList.toggle('d-none');
            prvScreen.classList.toggle('show-prev');
        },

        tipScrn: e => {

            let EditScreen = document.querySelector('#rEditor-p');
            let tipsScreen = document.querySelector('#tipsFrg');
            let resPrev = document.querySelector('#prev-bx');
            let Swidth = window.screen.width;
            let mpc = document.querySelector('#mainPContainer');

            if (Swidth < 991) {
                EditScreen?.classList.toggle('d-none')
                mpc?.classList.toggle('mbl-ofh')
            }


            if (Swidth < 991) {
                tipsScreen?.classList.toggle('showTips');

            } else {
                tipsScreen?.classList.toggle('d-lg-none');
                resPrev?.classList.toggle('d-none');
            }

        },
        }

    
    return(

    <>

        <section className="template-area">

            <Component {...props} {...methods} {...togMethod} />

        </section>

    </>


)
}


export default TemplateArea;