import { useRef, useState, useEffect } from 'react';
//import {useStyles} from '../../styles/styles';
import { useNavigate, Link } from 'react-router-dom';
import { get_root_url } from '../../utils/utils';
import Zoombtn from '../fragments/Zoombtn';

import './styles/templateContainer.css'

import FormPreloaderComponent from './FormPreloaderComponent';

export default function TemplatesComponent( props ) {
    
    const {templates, initCV, fH, isDisplayed, setFH} = props; 

    const formRef = useRef();
    const formHeight = useRef('0');
    

    useEffect(() => {
        formHeight.current = formRef.current.offsetHeight;
        
    }, [])

    useEffect(() => {
        console.log('fH', formRef.current.offsetHeight);
        setFH(parseInt(formRef.current.offsetHeight, 10));
    }, [formHeight])

    return (

        <div ref={formRef} className='templates-container' >
          <FormPreloaderComponent loadStyle={{
                                                     height: '100%',
                                                    opacity: '0.7',
                                                    top: '50%',
                                                    left: '50%',
                                                    transform: 'translate(-50%, -50%)',
                                                     display: `${isDisplayed}`
                                                }} />
        {
            templates.length > 0 
                ?
                   templates.map( ( item )=>{
                       //console.log('item', item)
                       return (
                           
                        <>

                               <div key={item.id} className="temp_hover col-md-3 col-lg-2 col-6">

                                   <div key={item}  className=" templ_bx">
                                       <img src={get_root_url + '/assets/images/Rectangle48.png'} alt="" />
                                   </div>


                                   <div className='hover-items'>
                                       
                                       <button className='btn btn-cstm' onClick={initCV} data-template-id={item.id}> 
                                           Use this template
                                       </button>

                                       <span className='btn btn-cstm zoombtn' data-bs-toggle="modal" data-bs-target="#templatePreview">
                                           <Zoombtn/>
                                       </span>

                                   </div>
                               </div>
                        </>
                       
                       )
                   } )
                   :
                   <div>No Templates Found</div>
               
        }
   
            
        </div>

    );
}

