import React, { useEffect } from 'react';
import { StyleCss } from './StyleCss';
import { get_root_url } from '../../../utils/utils';
import useCVData from '../../../hooks/useCVData';
import { Markup } from 'interweave';


const DefaultTemplate = React.forwardRef((props, ref)=> {

    const {educationData, experienceData,objectiveData,
        skillsData, languagesData, profileData,  } = useCVData(); 

    const dateFormat = (date)=>{
        let dateArr = date.split('-');
        dateArr.pop();
        //console.log('hi dateArr', dateArr);
        return dateArr.reverse().join('/');
        return 'hi';
    }

    return (
        < >
            <table ref={ref} style={StyleCss.table} className='templatePreview'>
                <tr style={StyleCss.alignTop}>
                    <td style={StyleCss.leftDetails}> 
                        <div>
                            <div style={StyleCss.leftTopBx}>

                                <img src={`${get_root_url}/assets/images/resumedemo.jpg`} style={StyleCss.imgBox} alt="" />

                                <div>
                                    <p style={StyleCss.applicantName}>
                                       { profileData.firstName ? `${profileData.firstName} ${profileData.lastName}` : 'SEAN BENET' } 
                                    </p>

                                    <span style={StyleCss.applicantDesig}>
                                        { profileData.occupation ? `${profileData.occupation}` : 'Fttx engineer' }
                                    </span>
                                </div>
                            </div>

                        </div>

                        

                        <div style={StyleCss.abtMe}>
                            <div style={StyleCss.paddingLtr}>

                                <div>
                                    <h6 style={StyleCss.leftSubHeadings}>
                                        ABOUT ME
                                    </h6>

                                    <div style={StyleCss.leftText}>
                                        <span>
                                            <Markup content={objectiveData} />
                                        </span>
                                    </div>
                                </div>

                                <div>

                                    <h6 style={StyleCss.leftSubHeadings}>
                                        CONTACT ME
                                    </h6>

                                    <div style={StyleCss.leftText}>
                                        <p>PHONE: <span> { profileData?.phone ? `+${profileData?.phone}` : '+9725231589555' }</span></p>
                                        <p>EMAIL: <span> { profileData?.email ? `${profileData?.email}` : 'Seanb18@yahoo.com' }</span></p>
                                        <p>COUNTRY: <span> { profileData?.nationality ? `${profileData?.nationality}` : 'Israel' }</span></p>
                                        <p>DATE OF BIRTH: <span> { profileData?.dob ? `${profileData?.dob}` : '22-07-99' }</span></p>
                                        <p>Portfolio: <span> { profileData?.link ? `${profileData?.link}` : '' }</span></p>
                                    </div>

                                </div>

                                <div>

                                    <h6 style={StyleCss.leftSubHeadings}>
                                        LANGUAGES
                                    </h6>

                                    <div style={StyleCss.leftText}>
                                        {
                                        languagesData.length > 0
                                                && languagesData.map((item, index)=>{
                                                    return (
                                                        <p key={item.id}>
                                                            {item.item}: 
                                                        <span >
                                                          100%   
                                                        </span>
                                                        </p>
                                                    )
                                                })
                                        }
                                    </div>

                                </div>

                                <div>

                                    <h6 style={StyleCss.leftSubHeadings}>
                                        SKILLS
                                    </h6>

                                    <div style={StyleCss.leftText}>
                                        {
                                        skillsData.length > 0
                                                && skillsData.map((item, index)=>{
                                                    return (
                                                        <span key={item.id}>
                                                            {item.item}&nbsp; 
                                                        </span>
                                                    )
                                                })
                                            
                                        }

                                    </div>

                                </div>
                                

                            </div>
                        </div>
                    </td>

                    <td style={StyleCss.rightDetails}>
                        <tr>
                            <td>
                               <tr>
                                   <td>
                                        <h2 style={StyleCss.rightHeadings}>
                                            WORK EXPERIENCE
                                        </h2>
                                   </td>
                               </tr>

                               <tr>
                                   <td>
                                   {
                                            experienceData.length > 0
                                                && 
                                                experienceData.map((item, index)=>{
                                                    return (
                                                        <tr>
                                                            <td>
                                                                <h6 style={StyleCss.rightSubHeadingsM}>
                                                                    <span>
                                                                        {dateFormat(item.startDate)} - {dateFormat(item.endDate)}
                                                                    </span>
                                                                </h6>

                                                                <p style={StyleCss.sumText}>
                                                                    <span>
                                                                        {item.title}<br/> 
                                                                        {item.company}<br/>{item.location} <br/><br />
                                                                        <Markup content={item.description} />
                                                                    </span>
                                                                </p>
                                                            </td>
                                                       </tr>
                                                    )
                                                })
                                        }
                                       
                                   </td>
                               </tr>
                            </td>
                        </tr>

                        <tr>
                            <td style={StyleCss.sectionRow}>
                                <tr>
                                    <td>
                                        <h2 style={StyleCss.rightHeadings}>
                                            EDUCATION
                                        </h2>
                                    </td>
                                </tr>

                                <tr>
                                    <td>
                                        {
                                            educationData.length > 0
                                                && 
                                                educationData.map((item, index)=>{
                                                    return (
                                                        <tr>
                                                            <td>
                                                                <h6 style={StyleCss.rightSubHeadingsM}>
                                                                    <span>
                                                                        {dateFormat(item.startDate)} - {dateFormat(item.endDate)}
                                                                    </span>
                                                                </h6>

                                                                <p style={StyleCss.sumText}>
                                                                    <span>
                                                                        {item.degree}<br/> 
                                                                        {item.college}<br/>{item.location} <br/><br />
                                                                        <Markup content={item.description} />
                                                                    </span>
                                                                </p>
                                                            </td>
                                                        </tr>
                                                    )
                                                })
                                        }
                                    </td>
                                </tr>
                            </td>
                        </tr>

                        <tr>
                            <td style={StyleCss.sectionRow}>
                                <tr>
                                    <td>
                                        <h2 style={StyleCss.rightHeadings}>
                                            SUMMARY
                                        </h2>
                                    </td>
                                </tr>

                                <tr>
                                    <td>
                                        <tr>
                                            <td>
                                                <p style={StyleCss.lastSum}>
                                                    <span>
                                                        As part of my high-capacity demonstrated roles, leading complex<br />
                                                        processes while finding a solution real-time issues and providing<br />
                                                        professional support and advice to a variety of factors within and<br />
                                                        outside the organization.
                                                    </span>
                                                </p>

                                                <p style={StyleCss.lastSum}>
                                                    <span>
                                                        I was also required to have high technical skill and creativity in<br />
                                                        solving complex problems.
                                                    </span>
                                                </p>

                                                <p style={StyleCss.lastSum}>
                                                    <span>
                                                        I am looking for positions of a technical and professional nature in<br />
                                                        order to express my skills in the best way.<br />
                                                        Recommendations will be accepted upon request.
                                                    </span>
                                                </p>
                                            </td>
                                        </tr>
                                    </td>
                                </tr>
                            </td>
                        </tr>


                    </td>
                </tr>
            </table>
        </>
    )
});

export default DefaultTemplate;