import PhoneIcon from "../fragments/PhoneIcon"

export default function ContactUsComponent( props ) {

    const { focusAndValid , blurAndValid } = props;
  return (
    <>
      <section className="contact-us">
          <div className="container-fluid">
             <div className="row">
                 <div className="col-lg-5 col-md-8">
                          <div className="top-info">
                              <h3>
                                  Contact Us
                              </h3>

                              <p>
                                  Talk to our assistance service and call us from
                                  Sunday to Friday from 9:00 - 16:00
                              </p>

                              <span>
                                  <a href="tel: 1700 555 475" className='btn btn-cstm'>  <PhoneIcon/>  1700 555 475 </a>
                              </span>
                          </div>

                          <div className="get-in-touch">

                              <h4>
                                  Did you find everything you’re looking for? <br/>
                                  Did we meet all of your needs?
                              </h4>

                              <span>
                                  For all questions or additional help and info please feel free to contact us and one
                                  of our team members will get back to you at our earliest convenience.
                              </span>

                              <form>
                                  <div className="col-xxl-6 col-xl-7 col-lg-8 col-md-12">
                                      <div className="row">
                                          <div className="full-name col-md-12 pi-input">
                                              <input type="text" id='name' required
                                                  onBlur={blurAndValid}
                                                  onFocus={focusAndValid}
                                              />
                                              <label> Name </label>

                                          </div>

                                          <div className="p-number col-md-12 pi-input">
                                              <input type="number" name='number' required 
                                                  onBlur={blurAndValid}
                                                  onFocus={focusAndValid}
                                              />
                                              <label className=""> Phone Number </label>
                                          </div>

                                          <div className="email-input col-md-12 pi-input">
                                              <input type="email" name='email' 
                                                  onBlur={blurAndValid}
                                                  onFocus={focusAndValid}
                                              required />
                                              <label className=""> Email Address </label>
                                          </div>

                                          <div className="col-md-12">
                                              <button className='btn btn-cstm'>
                                                  Send
                                              </button>
                                          </div>
                                      </div>
                                  </div>
                              </form>
                          </div>
                 </div>
             </div>
          </div>
      </section>
    </>
  )
}
