import { useTranslation } from 'react-i18next';
import i18next from 'i18next';
import cookies from 'js-cookie';

import { createContext, useEffect } from "react";

const LangContext = createContext({});

const languages = [
  {
    code: 'fr', 
    name: 'Français',
    country_code: 'fr',
  },
  {
    code: 'en',
    name: 'English',
    country_code: 'gb',
  },
  {
    code: 'ar',
    name: 'العربية',
    dir: 'rtl',
    country_code: 'sa',
  },
];

export const LangProvider = ({ children }) => {
    
	const currentLanguageCode = cookies.get('i18next') || 'en';
    const currentLanguage = languages.find((l) => l.code === currentLanguageCode);
    const { t } = useTranslation();

    useEffect(() => {
        if(document.querySelectorAll('.lang-select')?.length > 0 ){
            console.log('it exists');
            document.querySelectorAll('.lang-select').forEach((item, index)=>{
              console.log('item', item);
              item.dir = currentLanguage.dir || 'ltr';
            });
        }
        document.title = t('app_title');
      }, [currentLanguage, t]);

    return (
        <LangContext.Provider value={{ languages, currentLanguage, currentLanguageCode, t, i18next }}>
            {children}
        </LangContext.Provider>
    )
}

export default LangContext;