import React, { useState, useEffect, useRef } from 'react';
//import {useStyles} from '../../styles/styles';
import { useNavigate, useParams , Link } from 'react-router-dom';
import { get_root_url } from '../../utils/utils';
import { axiosPublic } from '../../services/http/axios';
import { toast , Slide } from 'react-toastify';

const RESET_API_URL = "api/users/security/reset-password";

export default function NewPasswordComponent( props ) {

    const { blurAndValid, focusAndValid, togglePwd, notify } = props;
    

    const { token } = useParams();
    //console.log( useParams );
    const [resetToken, setResetToken] = useState('');
    const [resp, setResp] = useState('test');
    const navigate = useNavigate();

    const passInput = useRef();
    const confPassInput = useRef();

    const handleSubmit = ()=>{
        let password = passInput.current.value;
        let confirmPassword = confPassInput.current.value;

        if( password.trim().replace("\\n", '') === '' ){
            console.log('Password can\'t be empty');
            return;
        }
        if( confirmPassword.trim().replace("\\n", '') === '' ){
            console.log('Confirm Password Field can\'t be empty');
            return;
        }

        axiosPublic.post(RESET_API_URL, 
            {token, password, password_confirmation: confirmPassword}
            )
        .then((response)=>{
            if( response.data.succ ){
                console.log('Success', response);
                //setResetToken(token);
                navigate('/login', true);
            }else{
                console.log('Error', response);
                //navigate('/login', true);
            }
        })
        .catch((err)=>{
            console.table(err);
        });

    }

    useEffect(()=>{

        axiosPublic.post('api/users/security/reset-check', {token: token}, {
                            headers: { 'Content-Type': 'application/json' },
                            withCredentials: true
                        })
            .then((response)=>{
                if( response.data.succ ){
                    console.log('Success', response);
                    setResetToken(token);

                }else{
                    console.log('Error', response);
                    navigate('/login', true);
                }
            })
            .catch((err)=>{
                console.table(err);
            });

    }, []);

    return (

        <div className="info-bx">

            <div className="form-logo">
                <img src={`${get_root_url}/assets/images/form-logo.svg`} alt="logo" />
            </div>

            <h2>
                Enter password {resp}
            </h2>


            <div className="form-bx">

                <input type="hidden"  value={token} />

                <div className="user-pswd">
                    <img src={`${get_root_url}/assets/images/password.svg`} alt="password" />
                    <input ref={passInput} type="password"
                        onFocus={focusAndValid}
                        onBlur={blurAndValid}
                    />
                    <label> New Password </label>
                    <img onClick={togglePwd} src={`${get_root_url}/assets/images/password-icon.svg`} alt="password eye" className="show-ps" />
                    <img src={`${get_root_url}/assets/images/eye-close.svg`} id="toggleNone" alt="password eye" className="show-ps crs" />
                </div>

                <div className="user-pswd">
                    <img src={`${get_root_url}/assets/images/password.svg`} alt="password" />
                    <input type="password" ref={confPassInput} 
                        onFocus={focusAndValid}
                        onBlur={blurAndValid}
                    />
                    <label> Confirm Password </label>
                    <img onClick={togglePwd} src={`${get_root_url}/assets/images/password-icon.svg`} alt="password eye" className="show-ps" />
                    
                </div>

                <button onClick={handleSubmit} className="btn btn-primary signup-btn">
                    Set
                </button>
            </div>

        </div>

    );
}