import useAuth from '../../hooks/useAuth';

//import {useStyles} from '../../styles/styles'; 
import { Navigate, Outlet } from 'react-router-dom';

export default function CheckLoginComponent(){

	const { auth } = useAuth();

	return (
		<>
			{
				auth?.accessToken ? 
						<Navigate to='/dashboard' replace={true} />
						: <Outlet/>
			}
		</>
		)
}