import { useState, useEffect } from 'react';
import {useNavigate} from 'react-router-dom';

import { get_root_url } from '../../utils/utils'
import {axiosAuthInstance} from '../../services/http/axios';
import useAuth from '../../hooks/useAuth';
import useCVData from '../../hooks/useCVData';

const DEFAULT_SECTIONS_FETCH_URL = 'api/cv/get/default-sections';

export default function MoreSectionComponent() {

    const {auth} = useAuth();

    const navigate = useNavigate();

    const axios = axiosAuthInstance(auth?.accessToken);

    const {cVData} = useCVData();

    const [defSecs, setDefSecs] = useState([]);

    useEffect(
            async () => {
                
                try {
                    const response = await axios.post(DEFAULT_SECTIONS_FETCH_URL);
                    console.log(response);
                    if( !response?.data?.succ ){
                        console.log('No Sections Found')
                        return;
                    }

                    setDefSecs((prevDefSecs)=>{
                        return [ ...response?.data?.sections ]
                    });
                    //END- Fetch DefSecs
                   
                } catch (err) {
                    console.log(err);
                }
            },
        []);

    useEffect(()=>{
        console.log('defSecs', defSecs);
    }, [defSecs]);

    return (
        <>
            <div className="profile-info-bx add-more-sec">
                <h3>
                    Add More Sections 
                </h3>

                <div className="row">

                    {
                        defSecs.length > 0 
                            && defSecs.map((item)=>{
                                return (
                                    <div onClick={e=>{
                                        navigate(`/resume/${cVData.cv_id}/add-section/${item.title}`, {replace: true})
                                    }} key={item.id} className="col-md-4 col-sm-6">
                                        <span className='section-box'>
                                            {item.title}
                                        </span>
                                    </div>
                                )
                            })

                    }
                    
                    <div className="col-md-12 mt-30">
                        <button
                            onClick={e=>{
                                        navigate(`/resume/${cVData.cv_id}/add-section`, {replace: true})
                                    }}
                          className='btn btn-cstm add-btn'>
                            Add custom Section
                        </button>    
                    </div>

                </div>

                <div className="profile-form">

                    <div className="row btm-btn-sec">

                        <div className="col-sm-4 col-6 btn-mt">
                            <button className='btn bk-edits-btn'>
                                Back
                            </button>
                        </div>

                        <div className="col-sm-4 col-6 btn-mt">
                            <button className='btn btn-cstm'>
                                Save
                            </button>
                        </div>
                    </div>

                </div>


            </div>

        </>
    )
}
