export default function AiInfoFragment() {
    return (
        <>
            <div className="col-xl-8 col-md-11 about-ai">

                <h2>
                    AI Information
                </h2>

                <div className=" contnt-bx ">

                    <div className="row justify-content-around">
                        
                        <div className="col-md-5">
                            <ul>
                                <li>
                                    AI Credit (Token) is a part of a word. 
                                    1 token is approximately 4 characters or 
                                    0.75 words in general, so if we translate 
                                    tokens to words. 100 tokens are 
                                    approximately 75 words.

                                </li>
                            </ul>
                        </div>

                        <div className="col-md-5">
                            <ul>
                                <li>
                                    With Pro Plan you will get monthly 
                                    300 AI Credits or you can purchase 
                                    AI Credits separately.
                                </li>
                            </ul>
                        </div> 

                    </div>

                </div>

            </div>

        </>
    )
}