import './RegistrationLayout.css'
import { toast, Slide } from 'react-toastify';


const RegistrationLayout = (Component) => (props) => { 
    

    const methods = {
        focusAndValid : e => {
            let label = e.target.nextElementSibling;
            label.classList.add('to-top');
        },
        blurAndValid : e => {

            if (e.target.value.trim().replace("\\n", '') != '') return;
            let label = e.target.nextElementSibling;
            label.classList.remove('to-top');
        },
        togglePwd : (e) => {
            let ele = e.target.previousElementSibling.previousElementSibling;
            let EyeSlash = document.querySelector('#togglePassword');
            let EyeSlashN = document.querySelector('#toggleNone');
            ele.getAttribute("type") === "password"
                ? ele.setAttribute("type", "text")
                : ele.setAttribute("type", "password");
            
                EyeSlashN.classList.toggle('d-block');
        },

        notify : (e) => {
            let loaderId = toast.loading("Please Wait", {
                position: "bottom-right",
                type: 'success',
                // theme: 'colored',
                // theme: 'dark',
                transition: Slide
            });      
        },

        startLoader : (e) =>{
            return toast.loading("Please Wait", {
                position: "bottom-right",
                type: 'success',
                // theme: 'colored',
                // theme: 'dark',
                transition: Slide
            });      
        },
        loaderCallback : (msg, loaderId, type)=>{
            toast.update(loaderId, {
                    render: msg,
                    type: type,
                    isLoading: false,
                    position: "bottom-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined
                });
        }
         
    }


    return (

		<>
            <section className="login-page">
        
                <Component {...props} {...methods}  />
           
            </section>
			
		</>

	)
}

export default RegistrationLayout;