import {useRef, useState, useEffect} from 'react';
import { get_root_url } from '../../utils/utils';

import useCVData from '../../hooks/useCVData';
import useAuth from '../../hooks/useAuth';

import customAsyncDispatcher from '../../hooks/customAsyncDispatcher';

import { useNavigate, Link } from "react-router-dom";

import { toast, Slide } from 'react-toastify';

import {KeyboardDatePicker} from '@material-ui/pickers';

import FormPreloaderComponent from './FormPreloaderComponent';
import useLang from '../../hooks/useLang'

export default function ProfileInfoComponent(props) {

  const { t } = useLang();

  const { blurAndValid, focusAndValid, togglePwd, startLoader, loaderCallback, checkIfNotEmpty } = props;

  const {cVData, dispatch, CV_ACTIONS,
      profileData, profileDispatch, PROFILE_ACTIONS} = useCVData();
  
  const navigate = useNavigate();

  const { auth } = useAuth();

  const formRef = useRef();
  const formHeight = useRef('0');
  const [fH, setFH] = useState('0');
  const [isDisplayed, setIsDisplayed] = useState('none');

  useEffect(() => {
        formHeight.current = formRef.current.offsetHeight;
        //focusAndValid()

        document.querySelectorAll('input').forEach((item)=>{
          checkIfNotEmpty(item);
        });
    }, [])

    useEffect(() => {
        setFH(parseInt(formRef.current.offsetHeight, 10)+20);
    }, [formHeight])

  const handleProfileInfoForm = async (e) =>{
      
      e.preventDefault();

      // if (pwd.trim().replace("\\n", '') == '') {
      //       toast('Password can\'t be empty', {
      //           type: "error",
      //           transition: Slide,
      //           position: "bottom-right",
      //       })
      //       return;
      //   }



      let req = { cv_id: cVData?.cv_id, ...profileData };

      setIsDisplayed('flex');
      let response = await customAsyncDispatcher(CV_ACTIONS.INSERT_PROFILE_INFO, req, auth);
      if(!response){
          setIsDisplayed('none');
          return;
      }
      setIsDisplayed('none');
      //loaderCallback(response.public_msg, loaderId, 'success');

      dispatch({type: CV_ACTIONS.INSERT_PROFILE_INFO, payload: req }); 
      navigate(`/resume/${cVData?.cv_id}/experience`, {replace: true});  

   }


  return (
    <>

    <div className="profile-info-bx">
        <h3>
          {t('profile_info')}

        </h3>

        <form ref={formRef} className="profile-form">
            <FormPreloaderComponent loadStyle={{
                                                     height: `${fH}px`,
                                                     display: isDisplayed 
                                                }} />
            <div className="row">

                    <div className="col-6 mw-400">
                        <div className="row">
                            <div className="f-name col-md-12 pi-input">
                              <input
                                  onChange={ e=>{

                                        profileDispatch( {type: PROFILE_ACTIONS.UPDATE_FIRSTNAME, payload: e.target.value} )
                                        //setFirstName(e.target.value)
                                    } } 
                                  value={ profileData.firstName } 

                                  type="text" id='f-name' required
                                  onBlur={blurAndValid}
                                  onFocus={focusAndValid}
                                />
                              <label> {t('first_name')} </label>

                          </div>

                          <div className="l-name col-md-12 pi-input">
                                  <input
                                    onBlur={blurAndValid}
                                    onFocus={focusAndValid}
                                    onChange={ e=>{

                                      profileDispatch( {type: PROFILE_ACTIONS.UPDATE_LASTNAME, payload: e.target.value} )
                                        //setLastName(e.target.value)
                                    } } 
                                    value={ profileData.lastName } 

                                    type="text" name='l-name'

                                  />
                              <label className="not-required"> {t('last_name')} </label>
                          </div>
                        </div>

                    </div>

                    <div className="upload-img col-6 mw-400">
                      <div className="row">
                          <div className="col-7 pi-input img-input">
                              <div className="pic-bg">
                                  <input type="file" id="img" name="img" accept="image/*" />
                                  <img src={`${get_root_url}/assets/images/inputimage.svg`} alt="user" />
                                  <label> {t('upload_pic')} </label>
                              </div>
                          </div>

                          <div className="col-5 d-flex justify-content-center align-items-center">
                              <div className="round-pic">
                                      <div className="form-check form-switch round-pic">
                                          <input className="form-check-input" type="checkbox" role="switch" id="flexSwitchCheckDefault" />
                                          <label className="form-check-label" htmlFor="flexSwitchCheckDefault">Round Pic</label>
                                      </div>
                              </div>



                          </div>
                      </div>
                    </div>



                      <div className="occ-input col-sm-6 pi-input">
                          <div className="row">
                            <div className="col-6 col-sm-12 mw-400">
                              <input
                            onChange={ e=>{
                                      profileDispatch( {type: PROFILE_ACTIONS.UPDATE_OCCUPATION, payload: e.target.value} )
                                        //setOccupation(e.target.value)
                                    } } 
                            value={ profileData.occupation} 
                            
                            type="text" name='Occupation' required
                              onBlur={blurAndValid}
                              onFocus={focusAndValid}
                            />
                          <label> {t('occupation')} </label>
                            </div>
                          </div>

                      </div>

                      <div className="nationality col-sm-6 pi-input">
                         <div className="row">
                           <div className="col-6 col-sm-12 mw-400">
                              <input

                            onChange={ e=>{
                                      profileDispatch( {type: PROFILE_ACTIONS.UPDATE_NATIONALITY, payload: e.target.value} )
                                        //setNationality(e.target.value)
                                    } } 
                            value={ profileData.nationality } 

                            type="text" name='nationality' required
                              onBlur={blurAndValid}
                              onFocus={focusAndValid}
                          />
                          <label> {t('nationality')} </label>
                           </div>
                         </div>
                      </div>

                      <div className="dob col-sm-6 pi-input">
                        <div className='row' >
                          <div className="col-6 col-sm-12 mw-400">
                          {/*<KeyboardDatePicker
                            label="DOB"
                            variant="inline" 
                            value={'22/12/2022'}
                            onChange={e=>{console.log('hi')}}
                          />*/}
                          <input 
                            
                            onChange={ e=>{
                                      
                                      profileDispatch( {type: PROFILE_ACTIONS.UPDATE_DOB, payload: e.target.value} )
                                        //setDob(e.target.value)
                                    } } 
                            value={ profileData.dob } 

                            type="date" name="dob" id=""
                              onBlur={blurAndValid}
                              onFocus={focusAndValid}
                          />
                          <label> DOB </label>
                            </div>
                          </div>
                      </div>

                      <div className="email col-sm-6 pi-input">
                          <input

                            onChange={ e=>{
                                      profileDispatch( {type: PROFILE_ACTIONS.UPDATE_EMAIL, payload: e.target.value} )
                                        //setEmail(e.target.value)
                                    } } 
                            value={ profileData.email } 

                            type="text" name='email' required
                              onBlur={blurAndValid}
                              onFocus={focusAndValid}
                          />
                          <label> {t('email_add')} </label>
                      </div>

                      <div className="link-bx col-sm-12 pi-input">
                          <input

                            onChange={ e=>{
                                      profileDispatch( {type: PROFILE_ACTIONS.UPDATE_LINK, payload: e.target.value} )
                                        //setLink(e.target.value)
                                    } } 
                            value={ profileData.link } 

                            type="text" name='link' required
                              onBlur={blurAndValid}
                              onFocus={focusAndValid}
                          />
                          <label> {t('link')} </label>
                      </div>
            </div>

            <div className="row btm-btn-sec">

                <div className="col-sm-4 col-6 btn-mt">
                    <Link to={"/resume/templates"} className='btn bk-edits-btn'>
                        {t('back')}
                    </Link>
                </div>

                <div className="col-sm-4 col-6 btn-mt">
                    <div onClick={handleProfileInfoForm} className='btn btn-cstm'>
                        {t('save')}
                    </div>
                </div>
            </div>

        </form>


    </div>

    </>
  )
}
