import { Outlet, useParams, useNavigate } from "react-router-dom";
import {useState, useEffect} from 'react';
import useCVData from './hooks/useCVData';
import useAuth from './hooks/useAuth';
import { axiosAuthInstance } from './services/http/axios';

import PreloaderComponent from './components/components/PreloaderComponent';

const GET_CV_DETAILS_URL = "api/cv/get/check-details";

const CheckParamsForCV = () => { 

    console.log()
    const { auth } = useAuth();
    const axios = axiosAuthInstance(auth?.accessToken);
    const { cv_id } = useParams();
    const [ isLoading, setIsLoading ] = useState(true);
    const [ isVerified, setIsVerified ] = useState(false);
    const { dispatch, CV_ACTIONS, educationDispatch, EDUCATION_ACTIONS,
            experienceDispatch, EXPERIENCE_ACTIONS,
            objectiveDispatch, OBJECTIVE_ACTIONS,
            skillsDispatch, SKILLS_ACTIONS,
            languagesDispatch, LANGUAGES_ACTIONS,
            profileDispatch, PROFILE_ACTIONS, } = useCVData();
    
    const navigate = useNavigate();

    useEffect(() => {
        let isMounted = true;

        const verifyCVId = async () => {
            try {
                 let res = await axios.post( GET_CV_DETAILS_URL, { cv_id } );
                 // if( typeof res.data === 'string' ){
                 //    res.data = JSON.parse(res.data);
                 // }
                 if( !res?.data.succ ){
                     console.log('error', res);
                     navigate('/resume/templates', {replace:true});
                 }
                 console.log('education response', res.data.payload.cvData.profileInfo);
                 dispatch( {type: CV_ACTIONS.FILL_CV_DATA, payload: res?.data?.payload } );
                 profileDispatch( {type: PROFILE_ACTIONS.INIT_PROFILE_DETAILS, payload: res.data.payload.cvData.profileInfo } );
                 skillsDispatch( {type: SKILLS_ACTIONS.INIT_SKILLS_DETAILS, payload: res.data.payload.cvData.skills } );
                 languagesDispatch( {type: LANGUAGES_ACTIONS.INIT_LANGUAGES_DETAILS, payload: res.data.payload.cvData.languages } );
                 objectiveDispatch( {type: OBJECTIVE_ACTIONS.INSERT_OBJECTIVE_DETAILS, payload: res.data.payload.cvData.objective } );
                 experienceDispatch( {type: EXPERIENCE_ACTIONS.INIT_EXPERIENCE_DETAILS, payload: res.data.payload.cvData.experience } );
                 educationDispatch( {type: EDUCATION_ACTIONS.INIT_EDUCATION_DETAILS, payload: res.data.payload.cvData.education } );
                 
                 setIsVerified(true);
            }
            catch (err) {
                console.error(err);
                navigate( '/resume/templates', {replace: true} )
            }
            finally {
                isMounted && setIsLoading(false);
            }
        }
        console.log('Auth access', auth?.accessToken);
        
        cv_id ? verifyCVId() : navigate( '/resume/templates', {replace:true} );

        return () => isMounted = false;
    }, []);

    return (
        <>
            { isLoading
                    ? < PreloaderComponent />
                    : isVerified 
                        ? <Outlet />
                        : < PreloaderComponent />
            }
        </>
    )
}

export default CheckParamsForCV;